import React, { useState } from 'react';
import { locales } from '../locales';

function ResultModal({
  show, role, results, setResults, handlePlayerSubmit, handleJudgeSubmit,
  penaltyTimers, teamAName, teamBName, setShowResultModal, setTeamAName, setTeamBName,
  resultsInputs, picksA, picksB, getTeamTotalCost, calcScore, draftMode, lang = 'ru'
}) {
  if (!show) return null;
  const t = locales[lang];
  const [showFormulas, setShowFormulas] = useState(false);

  // Вынесенная функция для судьи
  function renderJudgeSummary() {
    const getVal = (obj, k) => (obj && obj[k] !== undefined ? Number(obj[k]) : 0);

    function getDetails(prefix, picks, penalty) {
      if (draftMode === 'danger') {
        const battle1 = getVal(resultsInputs[prefix], `${prefix}_battle1`);
        const battle2 = getVal(resultsInputs[prefix], `${prefix}_battle2`);
        const battle3 = getVal(resultsInputs[prefix], `${prefix}_battle3`);
        const restart1 = getVal(resultsInputs[prefix], `${prefix}_restart1`);
        const restart2 = getVal(resultsInputs[prefix], `${prefix}_restart2`);
        const restart3 = getVal(resultsInputs[prefix], `${prefix}_restart3`);
        const teamValue = getTeamTotalCost ? getTeamTotalCost(picks) : 0;
        const penaltySec = penalty || 0;

        function rawScore(battleScore) {
          const s = Number(battleScore) || 0;
          if (s >= 65000) return 3000;
          if (s <= 20000) return 1000;
          return 1000 + ((s - 20000) / (65000 - 20000)) * (3000 - 1000);
        }

        const raw1 = rawScore(battle1);
        const raw2 = rawScore(battle2);
        const raw3 = rawScore(battle3);

        const totalRestarts = restart1 + restart2 + restart3;
        const restartPenalty = Math.max(0, totalRestarts - 2);
        const restartMultiplier = Math.pow(0.9, restartPenalty);

        let score = (raw1 + raw2 + raw3) * restartMultiplier;

        let gradePenalty = 0;
        [battle1, battle2, battle3].forEach(battleScore => {
          const s = Number(battleScore) || 0;
          if (s < 20000) {
            gradePenalty += 100;
            gradePenalty += 50 * Math.ceil((20000 - s) / 500);
          }
        });

        const maxValue = 1650;
        const multiplier = 2.5 - 1.75 * (teamValue / maxValue);

        const totalScore = (score - penaltySec * 1 - gradePenalty) * multiplier;

        return {
          battle1, battle2, battle3,
          restart1, restart2, restart3,
          teamValue, penaltySec, totalRestarts,
          raw1, raw2, raw3, restartPenalty, restartMultiplier, score, gradePenalty, multiplier, totalScore
        };
      }
      // shiyu-режим
      const totalTime = getVal(resultsInputs[prefix], `${prefix}_time`);
      const restart1 = getVal(resultsInputs[prefix], `${prefix}_restart1`);
      const restart2 = getVal(resultsInputs[prefix], `${prefix}_restart2`);
      const teamValue = getTeamTotalCost ? getTeamTotalCost(picks) : 0;
      const penaltySec = penalty || 0;

      const raw = 2000 - 4 * totalTime;
      const totalRestarts = restart1 + restart2;
      const restartPenalty = Math.max(0, totalRestarts - 2);
      const restartMultiplier = Math.pow(0.9, restartPenalty);
      const score = raw * restartMultiplier;
      const overtimePenalty = totalTime > 300 ? 100 + (totalTime - 300) * 4 : 0;
      const maxValue = picks.length === 9 ? 1650 : 1100;
      const multiplier = 2.5 - 1.75 * (teamValue / maxValue);
      const totalScore = (score - penaltySec * 2 - overtimePenalty) * multiplier;

      return {
        totalTime, restart1, restart2, teamValue, penaltySec, totalRestarts,
        raw, restartPenalty, restartMultiplier, score, overtimePenalty, multiplier, totalScore
      };
    }

    const detailsA = getDetails('A', picksA, penaltyTimers.A || 0);
    const detailsB = getDetails('B', picksB, penaltyTimers.B || 0);

    const renderDetails = (teamName, d) => {
      if (draftMode === 'danger') {
        return (
          <div style={{ marginBottom: 10 }}>
            <b>{teamName}</b>
            <ul>
              <li>{t.score1Short}{d.battle1} → {t.raw1}{Math.round(d.raw1)}</li>
              <li>{t.score2Short}{d.battle2} → {t.raw2}{Math.round(d.raw2)}</li>
              <li>{t.score3Short}{d.battle3} → {t.raw3}{Math.round(d.raw3)}</li>
              <li>{t.restarts}{d.restart1} + {d.restart2} + {d.restart3} = {d.totalRestarts}</li>
              <li>{t.penaltySeconds}{d.penaltySec}</li>
              <li>{t.teamCost}{d.teamValue}</li>
              <li>{t.restartPenalty}{d.totalRestarts} - 2) = {d.restartPenalty}</li>
              <li>{t.restartMultiplier}<sup>{d.restartPenalty}</sup> = {d.restartMultiplier.toFixed(4)}</li>
              <li>{t.score}({Math.round(d.raw1)} + {Math.round(d.raw2)} + {Math.round(d.raw3)}) × {d.restartMultiplier.toFixed(4)} = {Math.round(d.score)}</li>
              <li>{t.gradePenalty}{d.gradePenalty}</li>
              <li>{t.multiplier}{d.teamValue} / 1650) = {d.multiplier.toFixed(4)}</li>
              <li>
                <b>{t.summaryFormula}{Math.round(d.score)} - {d.penaltySec} × 1 - {d.gradePenalty}) × {d.multiplier.toFixed(4)} = <span style={{ color: '#1976d2' }}>{Math.round(d.totalScore)}</span></b>
              </li>
            </ul>
          </div>
        );
      }
      // shiyu
      return (
        <div style={{ marginBottom: 10 }}>
          <b>{teamName}</b>
          <ul>
            <li>{t.totalTimeLong}{Math.floor(d.totalTime / 60)} {t.minShort} {d.totalTime % 60} {t.secLong} ({d.totalTime} {t.secLong})</li>
            <li>{t.restarts}{d.restart1} + {d.restart2} = {d.totalRestarts}</li>
            <li>{t.penaltySeconds}{d.penaltySec}</li>
            <li>{t.teamCost}{d.teamValue}</li>
            <li>{t.raw}{d.totalTime} = {Math.round(d.raw)}</li>
            <li>{t.restartPenalty}{d.totalRestarts} - 2) = {d.restartPenalty}</li>
            <li>{t.restartMultiplier}<sup>{d.restartPenalty}</sup> = {d.restartMultiplier.toFixed(4)}</li>
            <li>{t.score}{Math.round(d.raw)} × {d.restartMultiplier.toFixed(4)} = {Math.round(d.score)}</li>
            <li>{t.overtimePenalty}{d.overtimePenalty}</li>
            <li>{t.multiplier}{d.teamValue} / {picksA.length === 9 ? 1650 : 1100}) = {d.multiplier.toFixed(4)}</li>
            <li>
              <b>{t.summaryFormula}{Math.round(d.score)} - {d.penaltySec} × 2 - {d.overtimePenalty}) × {d.multiplier.toFixed(4)} = <span style={{ color: '#1976d2' }}>{Math.round(d.totalScore)}</span></b>
            </li>
          </ul>
        </div>
      );
    };

    return (
      <div style={{
        marginTop: 16,
        background: '#181c22',
        borderRadius: 10,
        padding: 16,
        boxShadow: '0 2px 8px rgba(20,40,80,0.10)',
        maxWidth: showFormulas ? 420 : undefined, // Сделать компактнее при подробных расчетах
        width: showFormulas ? 420 : undefined,
        marginLeft: 'auto',
        marginRight: 'auto'
      }}>
        <h4 style={{ color: '#6ee7b7', fontWeight: 800, fontSize: 18, margin: 0, marginBottom: 10 }}>{t.scoringSummary}</h4>
        <div style={{ color: '#e3eafc', fontWeight: 700, marginBottom: 6 }}>
          <b>{teamAName}</b>: <b>{Math.round(detailsA.totalScore)}</b>
        </div>
        <div style={{ color: '#e3eafc', fontWeight: 700 }}>
          <b>{teamBName}</b>: <b>{Math.round(detailsB.totalScore)}</b>
        </div>
        <button
          type="button"
          style={{
            marginTop: 14,
            background: showFormulas
              ? 'linear-gradient(135deg, #2a3c5a 0%, #3a5a8c 100%)'
              : 'linear-gradient(135deg, #23262e 0%, #3a4a6a 100%)',
            color: showFormulas ? '#fff' : '#b8c7e0',
            border: 'none',
            borderRadius: 7,
            padding: '8px 20px',
            cursor: 'pointer',
            fontWeight: 700,
            fontSize: 15,
            boxShadow: showFormulas ? '0 2px 8px rgba(20,40,80,0.10)' : 'none',
            transition: 'background 0.2s, color 0.2s'
          }}
          onClick={() => setShowFormulas(f => !f)}
        >
          {showFormulas ? t.hideDetails : t.showDetails}
        </button>
        {showFormulas && (
          <div style={{
            marginTop: 14,
            background: '#23262e',
            borderRadius: 8,
            padding: 10,
            color: '#e3eafc',
            fontSize: 14,
            fontWeight: 500,
            boxShadow: '0 2px 8px rgba(20,40,80,0.10)',
            maxHeight: 260,
            overflowY: 'auto'
          }}>
            {renderDetails(teamAName, detailsA)}
            {renderDetails(teamBName, detailsB)}
          </div>
        )}
      </div>
    );
  }

  if (role === 'judge') {
    return (
      <div style={{
        position: 'fixed',
        top: 0, left: 0, right: 0, bottom: 0,
        background: 'rgba(0,0,0,0.45)',
        zIndex: 3000,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center'
      }}>
        <div style={{
          // <<< Здесь можно изменить размер окна результатов для судьи >>>
          background: 'linear-gradient(135deg, #23262e 80%, #1a1c22 100%)',
          padding: 28,
          borderRadius: 16,
          boxShadow: '0 8px 32px rgba(20,40,80,0.22)',
          minWidth: 500,
          maxWidth: 700,
          width: '100%',
          color: '#e3eafc',
          position: 'relative',
          border: '2.5px solid #1976d2',
          transition: 'box-shadow 0.2s',
        }}>
          {/* Крестик убран */}
          <h3 style={{
            margin: 0,
            marginBottom: 18,
            fontWeight: 900,
            fontSize: 26,
            color: '#6ee7b7',
            textAlign: 'center',
            letterSpacing: 0.5,
            textShadow: '0 2px 8px rgba(40,60,120,0.10)'
          }}>{t.finalResults}</h3>
          <form onSubmit={handleJudgeSubmit}>
            <div style={{
              display: 'flex',
              gap: 14,
              marginBottom: 16,
              flexWrap: 'wrap',
              justifyContent: 'space-between',
              alignItems: 'flex-start'
            }}>
              <div style={{
                flex: '1 1 0',
                minWidth: 0,
                maxWidth: '48%',
                marginRight: 0
                // <<< Здесь можно изменить размер поля для Команды А >>>
              }}>
                {/* Надпись Team A: всегда над полем */}
                <div style={{
                  fontSize: 15,
                  color: '#b8c7e0',
                  fontWeight: 700,
                  letterSpacing: 0.2,
                  marginBottom: 6
                }}>
                  {t.teamA}
                </div>
                <input
                  type="text"
                  value={teamAName}
                  // Сделать поле неактивным
                  readOnly
                  disabled
                  style={{
                    width: '70%',
                    padding: '10px 14px',
                    marginTop: 0,
                    borderRadius: 8,
                    border: '1.5px solid #3a4a6a',
                    background: '#23262e',
                    color: '#e3eafc',
                    fontSize: 17,
                    fontWeight: 700,
                    outline: 'none',
                    boxShadow: '0 1px 4px rgba(20,40,80,0.07)'
                    // <<< Здесь можно изменить размер input для Команды А >>>
                  }}
                  required
                />
              </div>
              <div style={{
                flex: '1 1 0',
                minWidth: 0,
                maxWidth: '48%',
                marginLeft: 0
                // <<< Здесь можно изменить размер поля для Команды Б >>>
              }}>
                {/* Надпись Team B: всегда над полем */}
                <div style={{
                  fontSize: 15,
                  color: '#b8c7e0',
                  fontWeight: 700,
                  letterSpacing: 0.2,
                  marginBottom: 6
                }}>
                  {t.teamB}
                </div>
                <input
                  type="text"
                  value={teamBName}
                  // Сделать поле неактивным
                  readOnly
                  disabled
                  style={{
                    width: '70%',
                    padding: '10px 14px',
                    marginTop: 0,
                    borderRadius: 8,
                    border: '1.5px solid #3a4a6a',
                    background: '#23262e',
                    color: '#e3eafc',
                    fontSize: 17,
                    fontWeight: 700,
                    outline: 'none',
                    boxShadow: '0 1px 4px rgba(20,40,80,0.07)'
                    // <<< Здесь можно изменить размер input для Команды Б >>>
                  }}
                  required
                />
              </div>
            </div>
            {/* Удалено: поля с Штраф команда А/Б */}
            {renderJudgeSummary()}
            <div style={{ marginTop: 18, display: 'flex', justifyContent: 'center', gap: 14 }}>
              <button type="submit" style={{
                background: 'linear-gradient(135deg, #2a3c5a 0%, #3a5a8c 100%)',
                color: '#fff',
                padding: '11px 28px',
                border: 'none',
                borderRadius: 9,
                cursor: 'pointer',
                fontSize: 18,
                fontWeight: 800,
                boxShadow: '0 2px 8px rgba(20,40,80,0.10)',
                letterSpacing: 0.5,
                transition: 'background 0.2s'
              }}>
                {t.confirm}
              </button>
              <button type="button" onClick={() => setShowResultModal(false)} style={{
                background: '#23262e',
                color: '#b8c7e0',
                border: '1.5px solid #3a4a6a',
                borderRadius: 9,
                padding: '11px 28px',
                fontSize: 18,
                fontWeight: 700,
                cursor: 'pointer',
                transition: 'background 0.2s'
              }}>
                {t.cancel}
              </button>
            </div>
          </form>
        </div>
      </div>
    );
  }

  // Игрок
  const prefix = role === 'playerA' ? 'A' : 'B';

  // --- UI для danger-режима ---
  if (draftMode === 'danger') {
    // Для danger-режима: три поля для счета боев, три для перезапусков
    const handleBattleChange = (battleNum, value) => {
      setResults({
        ...results,
        [`${prefix}_battle${battleNum}`]: value
      });
    };
    const handleRestartChange = (restartNum, value) => {
      setResults({
        ...results,
        [`${prefix}_restart${restartNum}`]: value
      });
    };
    // Убрано подтверждение через window.confirm
    const handlePlayerSubmitWithConfirm = (e) => {
      e.preventDefault();
      handlePlayerSubmit(e);
      setShowResultModal(false);
    };
    return (
      <div style={{
        // <<< Здесь можно изменить размер окна для ввода результатов у игроков >>>
        background: 'linear-gradient(135deg, #23262e 80%, #1a1c22 100%)',
        padding: 22,
        borderRadius: 16,
        boxShadow: '0 8px 32px rgba(20,40,80,0.22)',
        minWidth: 1000,
        maxWidth: 1000,
        minHeight: 260,
        maxHeight: 320,
        width: '100%',
        color: '#e3eafc',
        position: 'fixed', top: 0, left: 0, right: 0, bottom: 0, zIndex: 1000,
        margin: 'auto',
        border: '2.5px solid #1976d2',
        transition: 'box-shadow 0.2s',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center'
      }}>
        {/* Крестик убран */}
        <h3 style={{
          margin: 0,
          marginBottom: 16,
          fontWeight: 900,
          fontSize: 22,
          color: '#6ee7b7',
          textAlign: 'center',
          letterSpacing: 0.5,
          textShadow: '0 2px 8px rgba(40,60,120,0.10)'
        }}>{t.enterResultsDanger}</h3>
        <form onSubmit={handlePlayerSubmitWithConfirm}>
          <div style={{
            display: 'flex',
            flexDirection: 'row',
            gap: 32,
            justifyContent: 'center',
            alignItems: 'flex-end',
            marginBottom: 10,
            width: '100%'
          }}>
            {/* Счёт боя */}
            <div style={{
              flex: 1,
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center'
            }}>
              <label style={{
                fontSize: 14,
                color: '#b8c7e0',
                fontWeight: 600,
                marginBottom: 6,
                textAlign: 'center'
              }}>
                {t.score1}
              </label>
              <input
                type="text"
                inputMode="numeric"
                pattern="[0-9]*"
                value={results[`${prefix}_battle1`] || ''}
                onChange={e => handleBattleChange(1, e.target.value)}
                // <<< Здесь можно изменить размер поля для ввода счета боя 1 >>>
                style={{
                  width: '80%',
                  padding: '8px 10px',
                  borderRadius: 7,
                  border: '1.5px solid #3a4a6a',
                  background: '#181c22',
                  color: '#e3eafc',
                  fontSize: 15,
                  fontWeight: 600,
                  outline: 'none',
                  boxShadow: '0 1px 4px rgba(20,40,80,0.07)'
                }}
                required
              />
            </div>
            <div style={{
              flex: 1,
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center'
            }}>
              <label style={{
                fontSize: 14,
                color: '#b8c7e0',
                fontWeight: 600,
                marginBottom: 6,
                textAlign: 'center'
              }}>
                {t.score2}
              </label>
              <input
                type="text"
                inputMode="numeric"
                pattern="[0-9]*"
                value={results[`${prefix}_battle2`] || ''}
                onChange={e => handleBattleChange(2, e.target.value)}
                // <<< Здесь можно изменить размер поля для ввода счета боя 2 >>>
                style={{
                  width: '80%',
                  padding: '8px 10px',
                  borderRadius: 7,
                  border: '1.5px solid #3a4a6a',
                  background: '#181c22',
                  color: '#e3eafc',
                  fontSize: 15,
                  fontWeight: 600,
                  outline: 'none',
                  boxShadow: '0 1px 4px rgba(20,40,80,0.07)'
                }}
                required
              />
            </div>
            <div style={{
              flex: 1,
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center'
            }}>
              <label style={{
                fontSize: 14,
                color: '#b8c7e0',
                fontWeight: 600,
                marginBottom: 6,
                textAlign: 'center'
              }}>
                {t.score3}
              </label>
              <input
                type="text"
                inputMode="numeric"
                pattern="[0-9]*"
                value={results[`${prefix}_battle3`] || ''}
                onChange={e => handleBattleChange(3, e.target.value)}
                // <<< Здесь можно изменить размер поля для ввода счета боя 3 >>>
                style={{
                  width: '80%',
                  padding: '8px 10px',
                  borderRadius: 7,
                  border: '1.5px solid #3a4a6a',
                  background: '#181c22',
                  color: '#e3eafc',
                  fontSize: 15,
                  fontWeight: 600,
                  outline: 'none',
                  boxShadow: '0 1px 4px rgba(20,40,80,0.07)'
                }}
                required
              />
            </div>
          </div>
          <div style={{
            display: 'flex',
            flexDirection: 'row',
            gap: 32,
            justifyContent: 'center',
            alignItems: 'flex-end',
            marginBottom: 10,
            width: '100%'
          }}>
            {/* Перезапуски */}
            <div style={{
              flex: 1,
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center'
            }}>
              <label style={{
                fontSize: 14,
                color: '#b8c7e0',
                fontWeight: 600,
                marginBottom: 6,
                textAlign: 'center'
              }}>
                {t.restart1}
              </label>
              <input
                type="text"
                inputMode="numeric"
                pattern="[0-9]*"
                value={results[`${prefix}_restart1`] || ''}
                onChange={e => handleRestartChange(1, e.target.value)}
                // <<< Здесь можно изменить размер поля для ввода перезапусков 1 >>>
                style={{
                  width: '80%',
                  padding: '8px 10px',
                  borderRadius: 7,
                  border: '1.5px solid #3a4a6a',
                  background: '#181c22',
                  color: '#e3eafc',
                  fontSize: 15,
                  fontWeight: 600,
                  outline: 'none',
                  boxShadow: '0 1px 4px rgba(20,40,80,0.07)'
                }}
                required
              />
            </div>
            <div style={{
              flex: 1,
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center'
            }}>
              <label style={{
                fontSize: 14,
                color: '#b8c7e0',
                fontWeight: 600,
                marginBottom: 6,
                textAlign: 'center'
              }}>
                {t.restart2}
              </label>
              <input
                type="text"
                inputMode="numeric"
                pattern="[0-9]*"
                value={results[`${prefix}_restart2`] || ''}
                onChange={e => handleRestartChange(2, e.target.value)}
                // <<< Здесь можно изменить размер поля для ввода перезапусков 2 >>>
                style={{
                  width: '80%',
                  padding: '8px 10px',
                  borderRadius: 7,
                  border: '1.5px solid #3a4a6a',
                  background: '#181c22',
                  color: '#e3eafc',
                  fontSize: 15,
                  fontWeight: 600,
                  outline: 'none',
                  boxShadow: '0 1px 4px rgba(20,40,80,0.07)'
                }}
                required
              />
            </div>
            <div style={{
              flex: 1,
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center'
            }}>
              <label style={{
                fontSize: 14,
                color: '#b8c7e0',
                fontWeight: 600,
                marginBottom: 6,
                textAlign: 'center'
              }}>
                {t.restart3}
              </label>
              <input
                type="text"
                inputMode="numeric"
                pattern="[0-9]*"
                value={results[`${prefix}_restart3`] || ''}
                onChange={e => handleRestartChange(3, e.target.value)}
                // <<< Здесь можно изменить размер поля для ввода перезапусков 3 >>>
                style={{
                  width: '80%',
                  padding: '8px 10px',
                  borderRadius: 7,
                  border: '1.5px solid #3a4a6a',
                  background: '#181c22',
                  color: '#e3eafc',
                  fontSize: 15,
                  fontWeight: 600,
                  outline: 'none',
                  boxShadow: '0 1px 4px rgba(20,40,80,0.07)'
                }}
                required
              />
            </div>
          </div>
          <div style={{ marginTop: 14, display: 'flex', justifyContent: 'center', gap: 12 }}>
            <button type="submit" style={{
              background: 'linear-gradient(135deg, #2a3c5a 0%, #3a5a8c 100%)',
              color: '#fff',
              padding: '10px 24px',
              border: 'none',
              borderRadius: 8,
              cursor: 'pointer',
              fontSize: 16,
              fontWeight: 800,
              boxShadow: '0 2px 8px rgba(20,40,80,0.10)',
              letterSpacing: 0.5,
              transition: 'background 0.2s'
            }}>
              {t.sendResults}
            </button>
            <button type="button" onClick={() => setShowResultModal(false)} style={{
              background: '#23262e',
              color: '#b8c7e0',
              border: '1.5px solid #3a4a6a',
              borderRadius: 8,
              padding: '10px 24px',
              fontSize: 16,
              fontWeight: 700,
              cursor: 'pointer',
              transition: 'background 0.2s'
            }}>
              {t.cancel}
            </button>
          </div>
        </form>
      </div>
    );
  }

  // --- UI для shiyu (стандартный) ---
  const minutes = Number(results[`${prefix}_minutes`] || 0);
  const seconds = Number(results[`${prefix}_seconds`] || 0);

  const handleTimeChange = (min, sec) => {
    setResults({
      ...results,
      [`${prefix}_minutes`]: min,
      [`${prefix}_seconds`]: sec,
      [`${prefix}_time`]: String(Number(min) * 60 + Number(sec))
    });
  };

  // Убрано подтверждение через window.confirm
  const handlePlayerSubmitWithConfirm = (e) => {
    e.preventDefault();
    handlePlayerSubmit(e);
    setShowResultModal(false);
  };

  return (
    <div style={{
      // <<< Здесь можно изменить размер окна для ввода результатов у игроков (shiyu) >>>
      background: 'linear-gradient(135deg, #23262e 80%, #1a1c22 100%)',
      padding: 22,
      borderRadius: 16,
      boxShadow: '0 8px 32px rgba(20,40,80,0.22)',
      minWidth: 700,
      maxWidth: 700,
      minHeight: 260,
      maxHeight: 320,
      width: '100%',
      color: '#e3eafc',
      position: 'fixed', top: 0, left: 0, right: 0, bottom: 0, zIndex: 1000,
      margin: 'auto',
      border: '2.5px solid #1976d2',
      transition: 'box-shadow 0.2s',
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center'
    }}>
      {/* Крестик убран */}
      <h3 style={{
        margin: 0,
        marginBottom: 16,
        fontWeight: 900,
        fontSize: 22,
        color: '#6ee7b7',
        textAlign: 'center',
        letterSpacing: 0.5,
        textShadow: '0 2px 8px rgba(40,60,120,0.10)'
      }}>{t.enterResults}</h3>
      <form onSubmit={handlePlayerSubmitWithConfirm} style={{
        flex: 1,
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flex-start'
      }}>
        <div style={{
          display: 'flex',
          flexDirection: 'column',
          gap: 18,
          marginBottom: 10,
          alignItems: 'center',
          width: '100%'
        }}>
          {/* Total time row */}
          <div style={{
            width: '100%',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center'
          }}>
            <div style={{
              fontSize: 14,
              color: '#b8c7e0',
              fontWeight: 600,
              marginBottom: 4,
              textAlign: 'center'
            }}>
              {t.totalTime}
            </div>
            <div style={{
              display: 'flex',
              gap: 6,
              alignItems: 'center',
              justifyContent: 'center',
              marginBottom: 0
            }}>
              <select
                value={minutes}
                onChange={e => handleTimeChange(e.target.value, seconds)}
                style={{
                  padding: '8px 10px',
                  fontSize: 15,
                  borderRadius: 7,
                  border: '1.5px solid #3a4a6a',
                  background: '#181c22',
                  color: '#e3eafc',
                  fontWeight: 600,
                  outline: 'none'
                }}
                required
              >
                {Array.from({ length: 21 }).map((_, i) => (
                  <option key={i} value={i}>{i} {t.min}</option>
                ))}
              </select>
              <span>:</span>
              <select
                value={seconds}
                onChange={e => handleTimeChange(minutes, e.target.value)}
                style={{
                  padding: '8px 10px',
                  fontSize: 15,
                  borderRadius: 7,
                  border: '1.5px solid #3a4a6a',
                  background: '#181c22',
                  color: '#e3eafc',
                  fontWeight: 600,
                  outline: 'none'
                }}
                required
              >
                {Array.from({ length: 60 }).map((_, i) => (
                  <option key={i} value={i}>{i < 10 ? `0${i}` : i} {t.secShort}</option>
                ))}
              </select>
            </div>
          </div>
          {/* Restarts row */}
          <div style={{
            width: '100%',
            display: 'flex',
            flexDirection: 'row',
            gap: 18,
            justifyContent: 'center',
            alignItems: 'flex-end'
          }}>
            <div style={{ flex: 1, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
              <label style={{
                fontSize: 14,
                color: '#b8c7e0',
                fontWeight: 600,
                marginBottom: 4,
                textAlign: 'center'
              }}>
                {t.restart1}
              </label>
              <input
                type="text"
                inputMode="numeric"
                pattern="[0-9]*"
                value={results[`${prefix}_restart1`] || ''}
                onChange={e => setResults({ ...results, [`${prefix}_restart1`]: e.target.value })}
                // <<< Здесь можно изменить размер поля для ввода перезапусков >>>
                style={{
                  width: '70%',
                  padding: '8px 10px',
                  borderRadius: 7,
                  border: '1.5px solid #3a4a6a',
                  background: '#181c22',
                  color: '#e3eafc',
                  fontSize: 15,
                  fontWeight: 600,
                  outline: 'none',
                  boxShadow: '0 1px 4px rgba(20,40,80,0.07)'
                }}
                required
                // placeholder убран, поле по умолчанию пустое
              />
            </div>
            <div style={{ flex: 1, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
              <label style={{
                fontSize: 14,
                color: '#b8c7e0',
                fontWeight: 600,
                marginBottom: 4,
                textAlign: 'center'
              }}>
                {t.restart2}
              </label>
              <input
                type="text"
                inputMode="numeric"
                pattern="[0-9]*"
                value={results[`${prefix}_restart2`] || ''}
                onChange={e => setResults({ ...results, [`${prefix}_restart2`]: e.target.value })}
                // <<< Здесь можно изменить размер поля для ввода перезапусков >>>
                style={{
                  width: '70%',
                  padding: '8px 10px',
                  borderRadius: 7,
                  border: '1.5px solid #3a4a6a',
                  background: '#181c22',
                  color: '#e3eafc',
                  fontSize: 15,
                  fontWeight: 600,
                  outline: 'none',
                  boxShadow: '0 1px 4px rgba(20,40,80,0.07)'
                }}
                required
                // placeholder убран, поле по умолчанию пустое
              />
            </div>
          </div>
        </div>
        {/* Кнопки вниз посередине окна */}
        <div style={{
          marginTop: 'auto',
          display: 'flex',
          justifyContent: 'center',
          gap: 12
        }}>
          <button type="submit" style={{
            background: 'linear-gradient(135deg, #2a3c5a 0%, #3a5a8c 100%)',
            color: '#fff',
            padding: '10px 24px',
            border: 'none',
            borderRadius: 8,
            cursor: 'pointer',
            fontSize: 16,
            fontWeight: 800,
            boxShadow: '0 2px 8px rgba(20,40,80,0.10)',
            letterSpacing: 0.5,
            transition: 'background 0.2s'
          }}>
            {t.sendResults}
          </button>
          <button type="button" onClick={() => setShowResultModal(false)} style={{
            background: '#23262e',
            color: '#b8c7e0',
            border: '1.5px solid #3a4a6a',
            borderRadius: 8,
            padding: '10px 24px',
            fontSize: 16,
            fontWeight: 700,
            cursor: 'pointer',
            transition: 'background 0.2s'
          }}>
            {t.cancel}
          </button>
        </div>
      </form>
    </div>
  );
}

export default ResultModal;