import React, { useState, useEffect } from 'react';
import { agents, wEngines } from '../data/zzzData';
import { locales } from '../locales';

const WENGINE_CLASSES = [
  { key: 'Attack', ru: 'Нападение', en: 'Attack' },
  { key: 'Stun', ru: 'Устрашение', en: 'Stun' },
  { key: 'Anomaly', ru: 'Аномалия', en: 'Anomaly' },
  { key: 'Support', ru: 'Поддержка', en: 'Support' },
  { key: 'Defense', ru: 'Защита', en: 'Defense' },
  { key: 'Rupture', ru: 'Разрушение', en: 'Rupture' },
];

function PickAgentModal({
  show,
  agentName,
  onClose,
  onConfirm,
  lang = 'ru'
}) {
  const t = locales[lang];
  const [mindscape, setMindscape] = useState(0);
  const [wEngine, setWEngine] = useState(null);
  const [wEngineRank, setWEngineRank] = useState(0);
  const [wEngineSelectOpen, setWEngineSelectOpen] = useState(false);
  const [wEngineClassFilter, setWEngineClassFilter] = useState(null);

  // Сброс при открытии нового агента
  useEffect(() => {
    setMindscape(0);
    setWEngine(null);
    setWEngineRank(0);
    setWEngineSelectOpen(false);
    setWEngineClassFilter(null);
  }, [agentName, show]);

  if (!show || !agentName) return null;

  const agentObj = agents.find(a => a.name === agentName);

  function getAgentCost(agentName, mindscape) {
    const agent = agents.find(a => a.name === agentName);
    if (!agent) return 0;
    return agent.costs[mindscape] ?? 0;
  }

  function getWEngineCost(wEngineName, rank, agentName) {
    const w = wEngines.find(w => w.name === wEngineName);
    if (!w) return 0;
    const agent = agents.find(a => a.name === agentName);
    if (!agent) return 0;
    if (w.personal && agentName && w.personal[agentName]) {
      return w.personal[agentName][rank];
    }
    if (w.class && agent.class && w.class === agent.class) {
      return w.base[rank];
    }
    if (w.off !== undefined) {
      return w.off;
    }
    if (w.base && w.base.length > 5) {
      return w.base[5];
    }
    return w.base[rank];
  }

  // --- UI ---

  // Фильтрация w-engine по классу
  const filteredWEngines = wEngineClassFilter
    ? wEngines.filter(w => w.class === wEngineClassFilter)
    : wEngines;

  // Получить путь к иконке класса
  function getClassIconPath(classKey) {
    try {
      return require(`../data/Spec_pictures/${classKey}.png`);
    } catch {
      return null;
    }
  }

  // Получить локализованное название класса
  function getClassLabel(classKey) {
    const found = WENGINE_CLASSES.find(c => c.key === classKey);
    return found ? found[lang] || classKey : classKey;
  }

  // --- Всплывающее окно выбора w-engine ---
  function WEngineSelectModal() {
    if (!wEngineSelectOpen) return null;
    return (
      <div style={{
        position: 'fixed',
        top: 0, left: 0, right: 0, bottom: 0,
        background: 'rgba(0,0,0,0.45)',
        zIndex: 3000,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center'
      }}>
        <div style={{
          background: '#23262e',
          borderRadius: 14,
          padding: 28,
          minWidth: 420,
          maxWidth: 700,
          boxShadow: '0 8px 32px rgba(20,40,80,0.22)',
          color: '#e3eafc',
          position: 'relative',
          maxHeight: 540,
          overflowY: 'auto'
        }}>
          <button
            onClick={() => setWEngineSelectOpen(false)}
            style={{
              position: 'absolute',
              top: 12,
              right: 16,
              background: 'none',
              border: 'none',
              color: '#b8c7e0',
              fontSize: 28,
              cursor: 'pointer'
            }}
            aria-label="Close"
          >×</button>
          <div style={{ marginBottom: 18, fontWeight: 700, fontSize: 18 }}>
            {t.wengine}
          </div>
          {/* Фильтр по классам */}
          <div style={{ display: 'flex', gap: 10, marginBottom: 18, flexWrap: 'wrap' }}>
            <button
              onClick={() => setWEngineClassFilter(null)}
              style={{
                background: wEngineClassFilter === null ? '#6ee7b7' : '#23262e',
                color: wEngineClassFilter === null ? '#222' : '#e3eafc',
                border: '2px solid #3a4a6a',
                borderRadius: 8,
                padding: '6px 14px',
                fontWeight: 700,
                fontSize: 15,
                cursor: 'pointer',
                minWidth: 60
              }}
            >
              {lang === 'ru' ? 'Все' : 'All'}
            </button>
            {WENGINE_CLASSES.map(cls => (
              <button
                key={cls.key}
                onClick={() => setWEngineClassFilter(cls.key)}
                style={{
                  background: wEngineClassFilter === cls.key ? '#6ee7b7' : '#23262e',
                  color: wEngineClassFilter === cls.key ? '#222' : '#e3eafc',
                  border: '2px solid #3a4a6a',
                  borderRadius: 8,
                  padding: '6px 14px',
                  fontWeight: 700,
                  fontSize: 15,
                  cursor: 'pointer',
                  minWidth: 60,
                  display: 'flex',
                  alignItems: 'center',
                  gap: 6
                }}
              >
                {getClassIconPath(cls.key) && (
                  <img
                    src={getClassIconPath(cls.key)}
                    alt={cls.key}
                    style={{ width: 18, height: 18, marginRight: 2, verticalAlign: 'middle' }}
                  />
                )}
                {getClassLabel(cls.key)}
              </button>
            ))}
          </div>
          {/* Сетка плиток w-engine */}
          <div style={{
            display: 'grid',
            gridTemplateColumns: 'repeat(auto-fill, minmax(110px, 1fr))',
            gap: 16,
            maxHeight: 340,
            overflowY: 'auto'
          }}>
            {filteredWEngines.map(w => (
              <button
                key={w.name}
                type="button"
                onClick={() => {
                  setWEngine(w.name);
                  setWEngineSelectOpen(false);
                  setWEngineRank(0);
                }}
                style={{
                  background: wEngine === w.name ? '#6ee7b7' : '#181c22',
                  color: wEngine === w.name ? '#222' : '#e3eafc',
                  border: wEngine === w.name ? '2.5px solid #43d675' : '2px solid #3a4a6a',
                  borderRadius: 10,
                  padding: '10px 6px 8px 6px',
                  fontWeight: 600,
                  fontSize: 14,
                  cursor: 'pointer',
                  minHeight: 110,
                  minWidth: 0,
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'center',
                  justifyContent: 'flex-start',
                  transition: 'background 0.2s, color 0.2s, border 0.2s',
                  position: 'relative'
                }}
              >
                {/* Изображение w-engine */}
                <div style={{
                  width: 54,
                  height: 54,
                  background: '#23262e',
                  borderRadius: 8,
                  marginBottom: 6,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  overflow: 'hidden',
                  position: 'relative'
                }}>
                  <img
                    src={require(`../data/W-engine_pictures/${w.name}.png`)}
                    alt={w.nameLoc?.[lang] || w.name}
                    style={{
                      width: 48,
                      height: 48,
                      objectFit: 'cover',
                      borderRadius: 8,
                      background: '#23262e'
                    }}
                  />
                  {/* Иконка класса в правом верхнем углу */}
                  {w.class && getClassIconPath(w.class) && (
                    <img
                      src={getClassIconPath(w.class)}
                      alt={w.class}
                      style={{
                        position: 'absolute',
                        top: 2,
                        right: 2,
                        width: 18,
                        height: 18,
                        background: '#23262e',
                        borderRadius: 4,
                        border: '1.5px solid #3a4a6a'
                      }}
                    />
                  )}
                </div>
                {/* Название w-engine под плиткой */}
                <span style={{
                  textAlign: 'center',
                  fontWeight: 700,
                  fontSize: 13,
                  color: wEngine === w.name ? '#222' : '#e3eafc',
                  wordBreak: 'break-word',
                  marginTop: 2
                }}>
                  {w.nameLoc?.[lang] || w.name}
                </span>
              </button>
            ))}
          </div>
        </div>
      </div>
    );
  }

  return (
    <div style={{
      position: 'fixed',
      top: 0, left: 0, right: 0, bottom: 0,
      background: 'rgba(0,0,0,0.45)',
      zIndex: 2000,
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center'
    }}>
      <div style={{
        background: '#23262e',
        borderRadius: 12,
        padding: 28,
        minWidth: 340,
        maxWidth: 440,
        boxShadow: '0 4px 24px rgba(0,0,0,0.25)',
        color: '#e3eafc',
        position: 'relative'
      }}>
        <button
          onClick={onClose}
          style={{
            position: 'absolute',
            top: 12,
            right: 16,
            background: 'none',
            border: 'none',
            color: '#b8c7e0',
            fontSize: 22,
            cursor: 'pointer'
          }}
          aria-label="Close"
        >×</button>
        <h3 style={{ marginTop: 0, marginBottom: 12, fontWeight: 700 }}>
          {agentObj?.nameLoc?.[lang] || agentName}
        </h3>
        {/* Mindscape slider */}
        <div style={{ marginBottom: 18 }}>
          <div style={{ fontWeight: 600, marginBottom: 6 }}>{t.mindscape}</div>
          <div style={{ display: 'flex', alignItems: 'center', gap: 12 }}>
            <span style={{ fontWeight: 700, fontSize: 16, color: '#6ee7b7', minWidth: 32 }}>M{mindscape}</span>
            <input
              type="range"
              min={0}
              max={6}
              value={mindscape}
              onChange={e => setMindscape(Number(e.target.value))}
              style={{ flex: 1, accentColor: '#6ee7b7' }}
            />
            <span style={{
              fontWeight: 500,
              fontSize: 15,
              color: '#b8c7e0',
              marginLeft: 10,
              minWidth: 38
            }}>
              {getAgentCost(agentName, mindscape) === 999 ? 'X' : getAgentCost(agentName, mindscape)}
            </span>
          </div>
        </div>
        {/* W-Engine selection */}
        <div style={{ marginBottom: 12 }}>
          <div style={{ fontWeight: 600, marginBottom: 6 }}>{t.wengine}</div>
          <div style={{ display: 'flex', alignItems: 'center', gap: 10 }}>
            <button
              type="button"
              onClick={() => setWEngineSelectOpen(true)}
              style={{
                background: wEngine ? '#6ee7b7' : '#23262e',
                color: wEngine ? '#222' : '#e3eafc',
                border: '2px solid #3a4a6a',
                borderRadius: 8,
                padding: '8px 16px',
                fontWeight: 700,
                fontSize: 15,
                cursor: 'pointer',
                minWidth: 120,
                transition: 'background 0.2s, color 0.2s'
              }}
            >
              {wEngine
                ? (wEngines.find(w => w.name === wEngine)?.nameLoc?.[lang] || wEngine)
                : t.notSelected}
            </button>
            {/* Refinement slider for selected w-engine */}
            {wEngine && (
              <div style={{ display: 'flex', alignItems: 'center', gap: 8, marginLeft: 8 }}>
                <span style={{ color: '#b8c7e0', fontWeight: 500 }}>{t.refinement}</span>
                <input
                  type="range"
                  min={0}
                  max={4}
                  value={wEngineRank}
                  onChange={e => setWEngineRank(Number(e.target.value))}
                  style={{ accentColor: '#6ee7b7', width: 90 }}
                />
                <span style={{
                  color: '#6ee7b7',
                  fontWeight: 700,
                  minWidth: 32
                }}>
                  R{wEngineRank + 1}
                </span>
                <span style={{
                  color: '#b8c7e0',
                  fontWeight: 500,
                  minWidth: 32
                }}>
                  {getWEngineCost(wEngine, wEngineRank, agentName)}
                </span>
              </div>
            )}
          </div>
        </div>
        <div style={{ marginTop: 18 }}>
          <button
            onClick={() => onConfirm({ agent: agentName, mindscape, wEngine, wEngineRank })}
            style={{
              background: 'linear-gradient(135deg, #2a3c5a 0%, #3a5a8c 100%)',
              color: '#fff',
              padding: '10px 20px',
              border: 'none',
              borderRadius: 8,
              cursor: 'pointer',
              fontSize: 16,
              fontWeight: 700
            }}
          >✅ {t.confirm}</button>
        </div>
        {WEngineSelectModal()}
      </div>
    </div>
  );
}

export default PickAgentModal;
