import React, { useState, useEffect, useRef } from 'react';
import AgentList from './AgentList';
import TeamPanel from './TeamPanel';
import TimerPanel from './TimerPanel';
import ResultModal from './ResultModal';
import PickAgentModal from './PickAgentModal';
import { agents, wEngines } from '../data/zzzData';
import { locales } from '../locales';

// Убираем глобальный maxTeamValue, теперь он зависит от режима
const MAX_VALUES = {
  shiyu: 1100,
  danger: 1650,
};

// По умолчанию: 4 бана (2 на каждого), затем 18 пиков (9 на команду, поочередно B, A) — чтобы поддерживать danger-режим по умолчанию
const defaultTurnOrder = [
  { phase: 'ban', team: 'A' }, { phase: 'ban', team: 'B' },
  { phase: 'ban', team: 'A' }, { phase: 'ban', team: 'B' },
  ...Array.from({ length: 18 }, (_, i) => ({
    phase: 'pick',
    team: i % 2 === 0 ? 'B' : 'A'
  }))
];

// Тайминги (в секундах)
const MAIN_TIME = 30;      // основной таймер на ход
const RESERVE_TIME = 90;   // резервное время на игрока (на весь драфт) - увеличено до 90
const PENALTY_STEP = 1;    // шаг штрафного времени (секунд/тик)

function getTurnOrder(mode, winner) {
  // mode: 'shiyu' (6 picks) or 'danger' (9 picks)
  let order = [];
  if (mode === 'danger') {
    // 2 бана на каждого, 9 пиков на каждого (18 пиков)
    if (winner === 'A') {
      order = [
        { phase: 'ban', team: 'A' }, { phase: 'ban', team: 'B' },
        { phase: 'ban', team: 'A' }, { phase: 'ban', team: 'B' },
      ];
      for (let i = 0; i < 9; ++i) {
        order.push({ phase: 'pick', team: 'B' });
        order.push({ phase: 'pick', team: 'A' });
      }
    } else {
      order = [
        { phase: 'ban', team: 'B' }, { phase: 'ban', team: 'A' },
        { phase: 'ban', team: 'B' }, { phase: 'ban', team: 'A' },
      ];
      for (let i = 0; i < 9; ++i) {
        order.push({ phase: 'pick', team: 'A' });
        order.push({ phase: 'pick', team: 'B' });
      }
    }
  } else {
    // shiyu: 2 бана на каждого, 6 пиков на каждого (12 пиков)
    if (winner === 'A') {
      order = [
        { phase: 'ban', team: 'A' }, { phase: 'ban', team: 'B' },
        { phase: 'ban', team: 'A' }, { phase: 'ban', team: 'B' },
      ];
      for (let i = 0; i < 6; ++i) {
        order.push({ phase: 'pick', team: 'B' });
        order.push({ phase: 'pick', team: 'A' });
      }
    } else {
      order = [
        { phase: 'ban', team: 'B' }, { phase: 'ban', team: 'A' },
        { phase: 'ban', team: 'B' }, { phase: 'ban', team: 'A' },
      ];
      for (let i = 0; i < 6; ++i) {
        order.push({ phase: 'pick', team: 'A' });
        order.push({ phase: 'pick', team: 'B' });
      }
    }
  }
  return order;
}

// Константы для расчета результата
const RAW_MAX_SCORE = 2000; // максимальный счет за 0 секунд (обе половины объединены)
const RAW_SCORE_STEP = 4;   // сколько очков вычитается за каждую секунду (теперь 4, а не 2)
const S_RANK_TIME = 300;    // 5 минут в секундах
const OVERTIME_PENALTY_BASE = 100; // штраф за выход за S-ранг
const OVERTIME_PENALTY_PER_SEC = 4; // дополнительный штраф за каждую секунду сверх 5 минут

function DraftBoard({ roomId, role, socket, draftMode = 'shiyu', lang = 'ru' }) {
  const [bansA, setBansA] = useState([]);
  const [bansB, setBansB] = useState([]);
  const [picksA, setPicksA] = useState([]);
  const [picksB, setPicksB] = useState([]);
  const [selectedAgent, setSelectedAgent] = useState(null);
  const [currentTurnIndex, setCurrentTurnIndex] = useState(0);
  const [judgeOverride, setJudgeOverride] = useState(false);
  const [selectedWEngine, setSelectedWEngine] = useState(null);
  const [teamAName, setTeamAName] = useState('Команда A');
  const [teamBName, setTeamBName] = useState('Команда B');
  const [editA, setEditA] = useState(false);
  const [editB, setEditB] = useState(false);
  const [coinResult, setCoinResult] = useState(null);
  const [draftStarted, setDraftStarted] = useState(false);
  const [dynamicTurnOrder, setDynamicTurnOrder] = useState(defaultTurnOrder);
  const [playerRolls, setPlayerRolls] = useState({ A: null, B: null });

  // Таймеры для игроков
  const [mainTimers, setMainTimers] = useState({ A: MAIN_TIME, B: MAIN_TIME });
  const [reserveTimers, setReserveTimers] = useState({ A: RESERVE_TIME, B: RESERVE_TIME });
  const [penaltyTimers, setPenaltyTimers] = useState({ A: 0, B: 0 });
  const [timersActive, setTimersActive] = useState(false);

  // Для избежания лишних ререндеров и race condition
  const timersRef = useRef({
    main: { ...mainTimers },
    reserve: { ...reserveTimers },
    penalty: { ...penaltyTimers },
    timersActive,
    currentTurnIndex,
    currentPlayer: null,
  });

  // Сохраняем локально, что игрок уже вводил название (по roomId и роли)
  const localKey = `zzz-teamname-${roomId}-${role}`;
  const [teamNameSet, setTeamNameSet] = useState(() => {
    return !!localStorage.getItem(localKey);
  });

  // --- Синхронизируем локальное состояние названий с пропсами ---
  useEffect(() => {
    // Если получили с сервера новое название, обновляем локальное состояние
    if (role === 'playerA' && teamAName && teamAName !== 'Команда A') {
      localStorage.setItem(`zzz-teamname-${roomId}-playerA`, teamAName);
    }
    if (role === 'playerB' && teamBName && teamBName !== 'Команда B') {
      localStorage.setItem(`zzz-teamname-${roomId}-playerB`, teamBName);
    }
  }, [teamAName, teamBName, roomId, role]);

  // --- При монтировании и при изменении roomId/role, восстанавливаем названия из localStorage ---
  useEffect(() => {
    if (role === 'playerA') {
      const saved = localStorage.getItem(`zzz-teamname-${roomId}-playerA`);
      if (saved && saved !== teamAName) {
        setTeamAName(saved);
        socket.emit('teamNames', { roomId, teamA: saved });
      }
    }
    if (role === 'playerB') {
      const saved = localStorage.getItem(`zzz-teamname-${roomId}-playerB`);
      if (saved && saved !== teamBName) {
        setTeamBName(saved);
        socket.emit('teamNames', { roomId, teamB: saved });
      }
    }
    // eslint-disable-next-line
  }, [roomId, role]);

  const currentTurn = draftStarted ? (dynamicTurnOrder[currentTurnIndex] || null) : null;

  // Получить свою роль для жеребьевки
  const myPlayer = role === 'playerA' ? 'A' : role === 'playerB' ? 'B' : null;

  useEffect(() => {
    if (!socket) return;

    socket.on('roomState', (state) => {
      setBansA(state.bansA || []);
      setBansB(state.bansB || []);
      setPicksA(state.picksA || []);
      setPicksB(state.picksB || []);
      setCurrentTurnIndex(state.currentTurnIndex || 0);

      // Синхронизируем turnOrder с сервера (после жеребьевки)
      if (Array.isArray(state.turnOrder)) setDynamicTurnOrder(state.turnOrder);
      else setDynamicTurnOrder(getTurnOrder(draftMode, coinResult || 'A'));
      // Синхронизируем названия команд, жребий и статус драфта
      if (state.teamAName) setTeamAName(state.teamAName);
      if (state.teamBName) setTeamBName(state.teamBName);
      if (typeof state.coinResult !== 'undefined') setCoinResult(state.coinResult);
      setDraftStarted(!!state.draftStarted);

      // --- синхронизация жеребьёвки ---
      if (state.playerRolls) setPlayerRolls(state.playerRolls);

      // Если название уже есть на сервере, не спрашиваем у игрока и сохраняем в localStorage
      if (role === 'playerA' && state.teamAName && state.teamAName !== 'Команда A') {
        setTeamNameSet(true);
        localStorage.setItem(localKey, state.teamAName);
      }
      if (role === 'playerB' && state.teamBName && state.teamBName !== 'Команда B') {
        setTeamNameSet(true);
        localStorage.setItem(localKey, state.teamBName);
      }
    });

    socket.on('agentBanned', ({ team, agent }) => {
      if (team === 'A') setBansA(prev => [...prev, agent]);
      else setBansB(prev => [...prev, agent]);
      setCurrentTurnIndex(prev => prev + 1);
    });

    socket.on('agentChosen', ({ team, agent, mindscape, wEngine, wEngineRank }) => {
      const newPick = { agent, mindscape, wEngine, wEngineRank };
      if (team === 'A') setPicksA(prev => [...prev, newPick]);
      else setPicksB(prev => [...prev, newPick]);
      setCurrentTurnIndex(prev => prev + 1);
    });

    socket.on('teamNames', ({ teamA, teamB }) => {
      setTeamAName(teamA || 'Команда A');
      setTeamBName(teamB || 'Команда B');
      // Если название уже есть на сервере, не спрашиваем у игрока и сохраняем в localStorage
      if (role === 'playerA' && teamA && teamA !== 'Команда A') {
        setTeamNameSet(true);
        localStorage.setItem(localKey, teamA);
      }
      if (role === 'playerB' && teamB && teamB !== 'Команда B') {
        setTeamNameSet(true);
        localStorage.setItem(localKey, teamB);
      }
    });

    socket.on('rollResult', ({ player, value }) => {
      setPlayerRolls(prev => ({ ...prev, [player]: value }));
    });

    socket.on('rollsReset', () => {
      setPlayerRolls({ A: null, B: null });
      setCoinResult(null);
      setDraftStarted(false);
      setDynamicTurnOrder(defaultTurnOrder);
      setCurrentTurnIndex(0);
      setBansA([]);
      setBansB([]);
      setPicksA([]);
      setPicksB([]);
    });

    socket.on('coinFlip', (result) => {
      setCoinResult(result);
      // Формируем порядок ходов только если turnOrder не пришёл с сервера (fallback)
      if (!dynamicTurnOrder || dynamicTurnOrder === defaultTurnOrder) {
        setDynamicTurnOrder(getTurnOrder(draftMode, result));
      }
      setDraftStarted(true);
      setCurrentTurnIndex(0);
    });

    return () => {
      socket.off('roomState');
      socket.off('agentBanned');
      socket.off('agentChosen');
      socket.off('teamNames');
      socket.off('rollResult');
      socket.off('rollsReset');
      socket.off('coinFlip');
    };
  }, [socket, role, localKey, draftMode]);

  // Определяем победителя жеребьевки
  useEffect(() => {
    if (playerRolls.A && playerRolls.B && !coinResult) {
      let winner = null;
      if (playerRolls.A > playerRolls.B) winner = 'A';
      else if (playerRolls.B > playerRolls.A) winner = 'B';
      else winner = Math.random() < 0.5 ? 'A' : 'B'; // ничья — случайно
      setCoinResult(winner);
      setDynamicTurnOrder(getTurnOrder(draftMode, winner));
      setDraftStarted(true);
      setCurrentTurnIndex(0);
      socket.emit('coinFlip', { roomId, result: winner, draftMode }); // <--- draftMode теперь передаётся на сервер
    }
    // eslint-disable-next-line
  }, [playerRolls, coinResult, draftMode]);

  // Получить стоимость агента по mindscape
  function getAgentCost(agentName, mindscape) {
    const agent = agents.find(a => a.name === agentName);
    if (!agent) return 0;
    return agent.costs[mindscape] ?? 0;
  }

  // Получить стоимость w-engine для персонажа с учетом OFF (если классы не совпадают)
  function getWEngineCost(wEngineName, rank, agentName) {
    const w = wEngines.find(w => w.name === wEngineName);
    if (!w) return 0;
    const agent = agents.find(a => a.name === agentName);
    if (!agent) return 0;

    // Если есть индивидуальная стоимость для персонажа — всегда используем её
    if (w.personal && agentName && w.personal[agentName]) {
      return w.personal[agentName][rank];
    }

    // Если классы совпадают — обычная стоимость
    if (w.class && agent.class && w.class === agent.class) {
      return w.base[rank];
    }

    // Если классы не совпадают — используем OFF стоимость (обычно это 6-й элемент в message.txt)
    // В базе OFF обычно идет после R5, то есть индекс 5 или 6 (если есть персональные)
    // Для универсальности ищем поле off или берем base[5] если есть
    if (w.off !== undefined) {
      return w.off;
    }
    // Если OFF не выделто явно, берем base[5] если есть
    if (w.base && w.base.length > 5) {
      return w.base[5];
    }
    // Если OFF не найден — fallback на обычную стоимость
    return w.base[rank];
  }

  // Итоговая стоимость пика (агент + w-engine)
  function getPickTotalCost(agentName, mindscape, wEngineName, wEngineRank) {
    const agentCost = getAgentCost(agentName, mindscape);
    const wCost = wEngineName ? getWEngineCost(wEngineName, wEngineRank, agentName) : 0;
    return agentCost + wCost;
  }

  // Общая стоимость команды
  function getTeamTotalCost(picks) {
    return picks.reduce(
      (sum, pick) =>
        sum + getPickTotalCost(pick.agent, pick.mindscape, pick.wEngine, typeof pick.wEngineRank === 'number' ? pick.wEngineRank : 0),
      0
    );
  }

  // Используем picksCount для проверки лимита пиков
  const picksCount = draftMode === 'danger' ? 9 : 6;
  // Выбираем максимальную ценность по режиму
  const maxTeamValue = MAX_VALUES[draftMode] || 1100;

  const canAddAgent = (team, agentName, mindscape, wEngineName, wEngineRank) => {
    const newPickCost = getPickTotalCost(agentName, mindscape, wEngineName, wEngineRank);
    const picks = team === picksA ? picksA : picksB;
    if (picks.length >= picksCount) return false;
    return getTeamTotalCost(picks) + newPickCost <= maxTeamValue;
  };

  const handleBanAgent = (agentName) => {
    if (!currentTurn || currentTurn.phase !== 'ban' || !isMyTurn() || isBanned(agentName)) return;
    socket.emit('agentBanned', { roomId, team: currentTurn.team, agent: agentName });
  };

  // При выборе нового агента сбрасываем w-engine и его ранг
  useEffect(() => {
    setSelectedWEngine(null);
  }, [selectedAgent]);

  // Открытие модального окна выбора mindscape/w-engine
  function handleAgentPickOpen(agentName) {
    setSelectedAgent(agentName);
    setPickModalOpen(true);
  }

  // Подтверждение выбора из модального окна
  function handlePickAgentModalConfirm({ agent, mindscape, wEngine, wEngineRank }) {
    setPickModalOpen(false);
    setSelectedAgent(null);
    // Проверка: можно ли добавить агента (лимит стоимости)
    const team = currentTurn?.team === 'A' ? picksA : picksB;
    if (!canAddAgent(team, agent, mindscape, wEngine, wEngineRank)) {
      alert('Превышен лимит ценности!');
      return;
    }
    socket.emit('agentChosen', {
      roomId,
      team: currentTurn.team,
      agent,
      mindscape,
      wEngine: wEngine || null,
      wEngineRank: typeof wEngineRank === 'number' ? wEngineRank : 0,
    });
  }

  // Проверка: выбран ли агент уже в пиках (A или B)
  function isPicked(agentName) {
    // Для режима "Оборона Шиюй" (shiyu) — персонаж может быть выбран только один раз на весь драфт
    // Для режима "Опасный штурм" (danger) — персонаж может быть в обеих командах, но не дважды в одной
    if (draftMode === 'danger') {
      if (currentTurn?.team === 'A') {
        return picksA.some(p => p.agent === agentName);
      } else if (currentTurn?.team === 'B') {
        return picksB.some(p => p.agent === agentName);
      }
      return false;
    }
    return picksA.some(p => p.agent === agentName) || picksB.some(p => p.agent === agentName);
  }

  // Отправка названия команды
  const sendTeamName = (team, name) => {
    socket.emit('teamNames', {
      roomId,
      teamA: team === 'A' ? name : teamAName,
      teamB: team === 'B' ? name : teamBName,
    });
  };

  // Кнопка "Кинуть жребий" для игрока
  const handlePlayerRoll = () => {
    if (!myPlayer || playerRolls[myPlayer]) return;
    const value = Math.floor(Math.random() * 100) + 1;
    socket.emit('rollResult', { roomId, player: myPlayer, value });
  };

  // Кнопка "Сбросить жеребьевку" (для судьи)
  const handleResetRolls = () => {
    socket.emit('rollsReset', { roomId });
  };

  // При первом входе игрока — предложить ввести название команды, если оно не задано и не было задано ранее
  useEffect(() => {
    if (teamNameSet) return;
    // Проверяем, есть ли локально сохранённое название
    const savedName = localStorage.getItem(localKey);
    // --- исправлено: убираем зависимость от draftStarted ---
    if (role === 'playerA' && teamAName === 'Команда A') {
      if (savedName) {
        setTeamAName(savedName);
        setTeamNameSet(true);
        socket.emit('teamNames', { roomId, teamA: savedName, teamB: teamBName });
      } else {
        const name = prompt('Введите название вашей команды (A):', teamAName) || teamAName;
        setTeamAName(name);
        setTeamNameSet(true);
        localStorage.setItem(localKey, name);
        socket.emit('teamNames', { roomId, teamA: name, teamB: teamBName });
      }
    }
    if (role === 'playerB' && teamBName === 'Команда B') {
      if (savedName) {
        setTeamBName(savedName);
        setTeamNameSet(true);
        socket.emit('teamNames', { roomId, teamA: teamAName, teamB: savedName });
      } else {
        const name = prompt('Введите название вашей команды (B):', teamBName) || teamBName;
        setTeamBName(name);
        setTeamNameSet(true);
        localStorage.setItem(localKey, name);
        socket.emit('teamNames', { roomId, teamA: teamAName, teamB: name });
      }
    }
    // eslint-disable-next-line
  }, [role, teamAName, teamBName, socket, roomId, teamNameSet, localKey]);

  const isBanned = (agentName) => bansA.includes(agentName) || bansB.includes(agentName);

  const isMyTurn = () => {
    if (!currentTurn) return false;
    if (role === 'judge' && judgeOverride) return true;
    if (role === 'judge' && !judgeOverride) return false;
    if (role === 'playerA' && currentTurn.team === 'A') return true;
    if (role === 'playerB' && currentTurn.team === 'B') return true;
    return false;
  };

  // Синхронизация таймеров с сервера (и при первом подключении)
  useEffect(() => {
    if (!socket) return;

    const handleTimersSync = ({ main, reserve, penalty, timersActive: active }) => {
      setMainTimers(main);
      setReserveTimers(reserve);
      setPenaltyTimers(penalty);
      setTimersActive(active);
      timersRef.current = {
        ...timersRef.current,
        main,
        reserve,
        penalty,
        timersActive: active,
      };
    };

    socket.on('timersSync', handleTimersSync);

    return () => {
      socket.off('timersSync', handleTimersSync);
    };
  }, [socket]);

  // Определяем чей сейчас ход
  const currentPlayer = currentTurn ? currentTurn.team : null;

  // Слежение за стартом таймеров: активировать после первого бана
  useEffect(() => {
    if (role !== 'judge') return; // Только судья может запускать таймеры
    if (!timersActive && (bansA.length > 0 || bansB.length > 0)) {
      setTimersActive(true);
      timersSync(mainTimers, reserveTimers, penaltyTimers, true);
    }
    // eslint-disable-next-line
  }, [bansA, bansB, timersActive, role]);

  // Сброс основного таймера для игрока при наступлении его нового хода
  useEffect(() => {
    if (role !== 'judge') return; // Только судья может сбрасывать основной таймер
    if (!timersActive || !currentTurn) return;
    setMainTimers(timers => {
      const updated = { ...timers, [currentPlayer]: MAIN_TIME };
      timersSync(updated, reserveTimers, penaltyTimers, timersActive);
      return updated;
    });
    // eslint-disable-next-line
  }, [currentTurnIndex, timersActive, role]);

  // Основная логика тика таймеров (только судья управляет)
  useEffect(() => {
    if (role !== 'judge') return; // Только судья управляет тиками таймера
    if (!timersActive || !currentTurn) return;

    timersRef.current.currentPlayer = currentPlayer;
    timersRef.current.currentTurnIndex = currentTurnIndex;
    timersRef.current.timersActive = timersActive;

    let interval = setInterval(() => {
      setMainTimers(prev => {
        if (!timersRef.current.currentPlayer) return prev;
        if (prev[timersRef.current.currentPlayer] > 0) {
          const updated = { ...prev, [timersRef.current.currentPlayer]: prev[timersRef.current.currentPlayer] - 1 };
          timersRef.current.main = updated;
          timersSync(updated, timersRef.current.reserve, timersRef.current.penalty, timersRef.current.timersActive);
          return updated;
        }
        return prev;
      });

      setReserveTimers(prev => {
        if (!timersRef.current.currentPlayer) return prev;
        if (
          timersRef.current.main[timersRef.current.currentPlayer] === 0 &&
          prev[timersRef.current.currentPlayer] > 0
        ) {
          const updated = { ...prev, [timersRef.current.currentPlayer]: prev[timersRef.current.currentPlayer] - 1 };
          timersRef.current.reserve = updated;
          timersSync(timersRef.current.main, updated, timersRef.current.penalty, timersRef.current.timersActive);
          return updated;
        }
        return prev;
      });

      setPenaltyTimers(prev => {
        if (!timersRef.current.currentPlayer) return prev;
        if (
          timersRef.current.main[timersRef.current.currentPlayer] === 0 &&
          timersRef.current.reserve[timersRef.current.currentPlayer] === 0
        ) {
          const updated = { ...prev, [timersRef.current.currentPlayer]: prev[timersRef.current.currentPlayer] + PENALTY_STEP };
          timersRef.current.penalty = updated;
          timersSync(timersRef.current.main, timersRef.current.reserve, updated, timersRef.current.timersActive);
          return updated;
        }
        return prev;
      });
    }, 1000);

    return () => clearInterval(interval);
    // eslint-disable-next-line
  }, [timersActive, currentTurnIndex, mainTimers, reserveTimers, currentTurn, role]);

  // Сброс таймеров при сбросе драфта
  useEffect(() => {
    if (role !== 'judge') return; // Только судья сбрасывает таймеры
    if (!timersActive && (bansA.length === 0 && bansB.length === 0)) {
      setMainTimers({ A: MAIN_TIME, B: MAIN_TIME });
      setReserveTimers({ A: RESERVE_TIME, B: RESERVE_TIME });
      setPenaltyTimers({ A: 0, B: 0 });
      timersSync({ A: MAIN_TIME, B: MAIN_TIME }, { A: RESERVE_TIME, B: RESERVE_TIME }, { A: 0, B: 0 }, false);
    }
    // eslint-disable-next-line
  }, [timersActive, bansA, bansB, role]);

  // Функция для отправки таймеров на сервер (только судья!)
  const timersSync = (main, reserve, penalty, timersActive) => {
    if (!socket) return;
    if (role === 'judge') {
      socket.emit('timersSync', {
        roomId,
        main,
        reserve,
        penalty,
        timersActive,
      });
    }
  };

  // Новый компонент для таймера одного игрока
  function PlayerTimerPanel({ teamName, main, reserve, penalty, isActive, t }) {
    return (
      <div style={{
        background: '#222b',
        borderRadius: 8,
        padding: '6px 10px',
        margin: 0,
        color: isActive ? '#6ee7b7' : '#e3eafc',
        fontWeight: isActive ? 700 : 500,
        fontSize: 14,
        minWidth: 110,
        textAlign: 'center'
      }}>
        <div style={{ fontWeight: 700, fontSize: 15, marginBottom: 2 }}>{teamName}</div>
        <div>
          {main} {t.sec} | {t.reserve}<b>{reserve}</b> {t.sec} | {t.penalty}<b>{penalty}</b> {t.sec}
        </div>
      </div>
    );
  }

  // Новое состояние для модального окна и результатов
  const [showResultModal, setShowResultModal] = useState(false);
  const [results, setResults] = useState({
    A_time: '', A_restart1: '', A_restart2: '',
    B_time: '', B_restart1: '', B_restart2: ''
  });
  const [finalResult, setFinalResult] = useState(null);
  const [resultsInputs, setResultsInputs] = useState({ A: null, B: null }); // для синхронизации ввода

  // Получение итогов с сервера
  useEffect(() => {
    if (!socket) return;
    socket.on('draftResults', (data) => {
      setFinalResult(data);
      setShowResultModal(false);
    });
    socket.on('openResultsModal', () => {
      setShowResultModal(true);
    });
    return () => {
      socket.off('draftResults');
      socket.off('openResultsModal');
    };
  }, [socket]);

  // Синхронизация ввода результатов между всеми клиентами
  useEffect(() => {
    if (!socket) return;
    socket.on('draftResultsInput', (data) => {
      setResultsInputs(prev => ({ ...prev, ...data }));
      // Если это мой ввод, обновим локально форму
      if (role === 'playerA' && data.A) setResults(r => ({ ...r, ...data.A }));
      if (role === 'playerB' && data.B) setResults(r => ({ ...r, ...data.B }));
    });
    return () => {
      socket.off('draftResultsInput');
    };
  }, [socket, role]);

  // Открытие окна для всех (по команде судьи)
  const openResultModal = () => {
    if (socket) socket.emit('openResultsModal', { roomId });
  };

  // Игрок отправляет свои результаты
  const handlePlayerSubmit = (e) => {
    e.preventDefault();
    const prefix = role === 'playerA' ? 'A' : 'B';
    let fields;
    if (draftMode === 'danger') {
      fields = [
        `${prefix}_battle1`, `${prefix}_battle2`, `${prefix}_battle3`,
        `${prefix}_restart1`, `${prefix}_restart2`, `${prefix}_restart3`
      ];
    } else {
      fields = [`${prefix}_time`, `${prefix}_restart1`, `${prefix}_restart2`];
    }
    for (const f of fields) {
      if (results[f] === '' || isNaN(Number(results[f]))) {
        alert('Заполните все поля корректно');
        return;
      }
    }
    const data = {};
    if (draftMode === 'danger') {
      data[prefix] = {
        [`${prefix}_battle1`]: results[`${prefix}_battle1`],
        [`${prefix}_battle2`]: results[`${prefix}_battle2`],
        [`${prefix}_battle3`]: results[`${prefix}_battle3`],
        [`${prefix}_restart1`]: results[`${prefix}_restart1`],
        [`${prefix}_restart2`]: results[`${prefix}_restart2`],
        [`${prefix}_restart3`]: results[`${prefix}_restart3`],
      };
    } else {
      data[prefix] = {
        [`${prefix}_time`]: results[`${prefix}_time`],
        [`${prefix}_restart1`]: results[`${prefix}_restart1`],
        [`${prefix}_restart2`]: results[`${prefix}_restart2`],
      };
    }
    socket.emit('draftResultsInput', { roomId, ...data });
  };

  // Судья отправляет финальный результат
  const handleJudgeSubmit = (e) => {
    e.preventDefault();
    // penaltyTimers берём из состояния
    const penaltyA = penaltyTimers.A || 0;
    const penaltyB = penaltyTimers.B || 0;
    const getVal = (obj, k) => (obj && obj[k] !== undefined ? obj[k] : '');

    let scoreA, scoreB;
    if (draftMode === 'danger') {
      // Используем правильные поля для danger-режима
      scoreA = Math.round(calcScore({
        battle1: getVal(resultsInputs.A, 'A_battle1'),
        battle2: getVal(resultsInputs.A, 'A_battle2'),
        battle3: getVal(resultsInputs.A, 'A_battle3'),
        restart1: getVal(resultsInputs.A, 'A_restart1'),
        restart2: getVal(resultsInputs.A, 'A_restart2'),
        restart3: getVal(resultsInputs.A, 'A_restart3'),
        penalty: penaltyA,
        teamValue: getTeamTotalCost(picksA)
      }));
      scoreB = Math.round(calcScore({
        battle1: getVal(resultsInputs.B, 'B_battle1'),
        battle2: getVal(resultsInputs.B, 'B_battle2'),
        battle3: getVal(resultsInputs.B, 'B_battle3'),
        restart1: getVal(resultsInputs.B, 'B_restart1'),
        restart2: getVal(resultsInputs.B, 'B_restart2'),
        restart3: getVal(resultsInputs.B, 'B_restart3'),
        penalty: penaltyB,
        teamValue: getTeamTotalCost(picksB)
      }));
    } else {
      // shiyu
      scoreA = Math.round(calcScore({
        totalTime: getVal(resultsInputs.A, 'A_time'),
        restart1: getVal(resultsInputs.A, 'A_restart1'),
        restart2: getVal(resultsInputs.A, 'A_restart2'),
        penalty: penaltyA,
        teamValue: getTeamTotalCost(picksA)
      }));
      scoreB = Math.round(calcScore({
        totalTime: getVal(resultsInputs.B, 'B_time'),
        restart1: getVal(resultsInputs.B, 'B_restart1'),
        restart2: getVal(resultsInputs.B, 'B_restart2'),
        penalty: penaltyB,
        teamValue: getTeamTotalCost(picksB)
      }));
    }
    let winner = null;
    if (scoreA > scoreB) winner = 'A';
    else if (scoreB > scoreA) winner = 'B';
    else winner = 'draw';
    socket.emit('draftResults', {
      roomId,
      scoreA,
      scoreB,
      winner,
      teamAName,
      teamBName,
      details: {
        A: { ...resultsInputs.A, penalty: penaltyA, score: scoreA },
        B: { ...resultsInputs.B, penalty: penaltyB, score: scoreB }
      }
    });
  };

  // Вспомогательная функция для расчета счета (разные режимы)
  function calcScore({
    totalTime, // для shiyu
    restart1,
    restart2,
    restart3, // для danger
    penalty,
    teamValue,
    // для danger:
    battle1,
    battle2,
    battle3
  }) {
    if (draftMode === 'danger') {
      // Опасный штурм: расчет по 3 боям, ценность 1650, нет времени
      const maxValue = 1650;
      const value = Number(teamValue) || 0;
      const r1 = Math.max(0, Number(restart1) || 0);
      const r2 = Math.max(0, Number(restart2) || 0);
      const r3 = Math.max(0, Number(restart3) || 0);
      const penaltySec = Number(penalty) || 0;

      // Сырые очки за каждый бой (от 0 до 3000)
      function rawScore(battleScore) {
        const s = Number(battleScore) || 0;
        if (s >= 65000) return 3000;
        if (s <= 20000) return 1000;
        // Линейная интерполяция между 20000 и 65000
        return 1000 + ((s - 20000) / (65000 - 20000)) * (3000 - 1000);
      }

      const raw1 = rawScore(battle1);
      const raw2 = rawScore(battle2);
      const raw3 = rawScore(battle3);

      // Перезапуски
      const totalRestarts = r1 + r2 + r3;
      const restartPenalty = Math.max(0, totalRestarts - 2);
      const restartMultiplier = Math.pow(0.9, restartPenalty);

      // Итоговый сырой счет
      let score = (raw1 + raw2 + raw3) * restartMultiplier;

      // Штраф за низкий счет (менее 20000 в любом бою)
      let gradePenalty = 0;
      [battle1, battle2, battle3].forEach(battleScore => {
        const s = Number(battleScore) || 0;
        if (s < 20000) {
          gradePenalty += 100;
          gradePenalty += 50 * Math.ceil((20000 - s) / 500);
        }
      });

      // Множитель за ценность
      const multiplier = 2.5 - 1.75 * (value / maxValue);

      // Итоговый счет
      let totalScore = (score - penaltySec * RAW_SCORE_STEP / 2 - gradePenalty) * multiplier;
      return totalScore;
    } else {
      // Оборона Шиюй (обновленная формула)
      const t = Number(totalTime) || 0;
      const r1 = Math.max(0, Number(restart1) || 0);
      const r2 = Math.max(0, Number(restart2) || 0);
      const penaltySec = Number(penalty) || 0;
      const value = Number(teamValue) || 0;

      let raw = RAW_MAX_SCORE - (RAW_SCORE_STEP * t);

      const totalRestarts = r1 + r2;
      const restartPenalty = Math.max(0, totalRestarts - 2);
      const restartMultiplier = Math.pow(0.9, restartPenalty);

      let score = raw * restartMultiplier;

      let overtimePenalty = 0;
      if (t > S_RANK_TIME) {
        overtimePenalty = OVERTIME_PENALTY_BASE + (t - S_RANK_TIME) * OVERTIME_PENALTY_PER_SEC;
      }

      const maxValue = draftMode === 'danger' ? 1650 : 1100;
      const multiplier = 2.5 - 1.75 * (value / maxValue);

      let totalScore = (score - penaltySec * RAW_SCORE_STEP / 2 - overtimePenalty) * multiplier;
      return totalScore;
    }
  }

  // Проверка завершения драфта (все пики сделаны)
  const draftComplete =
    draftStarted &&
    picksA.length >= picksCount &&
    picksB.length >= picksCount;

  const t = locales[lang];

  // --- Центральные блоки вынесены в отдельные компоненты ---

  // Названия команд и жеребьевка
  function TeamNamesAndCoin() {
    // Кнопка-тайл для ролла (стиль как в AgentList)
    function RollTile({ player, value, onRoll, disabled, align }) {
      return (
        <div style={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: align === 'left' ? 'flex-start' : align === 'right' ? 'flex-end' : 'center',
          flex: 1,
          minWidth: 90,
          maxWidth: 110,
        }}>
          <button
            onClick={onRoll}
            disabled={disabled}
            style={{
              width: 78,
              height: 74,
              background: '#23262e',
              borderRadius: 8,
              boxShadow: '0 2px 8px rgba(20,40,80,0.10)',
              border: disabled ? '2px solid #444' : value !== null ? '2px solid #6ee7b7' : '2px solid #1976d2',
              cursor: disabled ? 'not-allowed' : 'pointer',
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              justifyContent: 'flex-start',
              padding: 0,
              margin: 0,
              transition: 'border 0.2s, box-shadow 0.2s',
              userSelect: 'none',
              outline: 'none',
              position: 'relative',
              overflow: 'hidden'
            }}
          >
            {/* Верхняя часть — число или "-" */}
            <div
              style={{
                width: '100%',
                height: 40,
                background: '#181c22',
                borderTopLeftRadius: 8,
                borderTopRightRadius: 8,
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: 22,
                color: value !== null ? '#6ee7b7' : '#b8c7e0',
                fontWeight: 700,
                borderBottom: '1px solid #222',
                textAlign: 'center',
                overflow: 'hidden',
                whiteSpace: 'nowrap',
                width: '100%',
                letterSpacing: 0.5
              }}
            >
              {value !== null ? value : '-'}
            </div>
            {/* Нижняя часть — "Ролл" */}
            <div
              style={{
                width: '100%',
                height: 34,
                textAlign: 'center',
                fontWeight: 600,
                fontSize: 13,
                color: '#e3eafc',
                padding: '4px 2px 6px 2px',
                borderBottomLeftRadius: 8,
                borderBottomRightRadius: 8,
                background: 'none',
                overflow: 'hidden',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                textOverflow: 'ellipsis',
                whiteSpace: 'normal',
                wordBreak: 'break-word',
              }}
            >
              {t.rollButton}
            </div>
          </button>
        </div>
      );
    }

    return (
      <div style={{
        marginBottom: 18,
        display: 'flex',
        flexDirection: 'row',
        background: 'rgba(40,44,54,0.96)',
        borderRadius: 10,
        padding: '18px 18px 10px 18px',
        boxShadow: '0 2px 8px rgba(20,40,80,0.10)',
        alignItems: 'flex-start',
        width: '100%',
        maxWidth: 1130,
        minWidth: 1130,
        marginLeft: '-44%',
        marginRight: 'auto',
        justifyContent: 'space-between',
        gap: 10
      }}>
        {/* Roll tile for Player A (left) + таймеры справа от плитки */}
        <div style={{
          flex: 1,
          display: 'flex',
          flexDirection: 'row',
          alignItems: 'center',
          justifyContent: 'flex-start',
          gap: 10,
          minWidth: 120,
        }}>
          <div>
            {myPlayer === 'A' && !playerRolls.A && !draftStarted ? (
              <RollTile
                player="A"
                value={playerRolls.A}
                onRoll={handlePlayerRoll}
                disabled={!!playerRolls.A || draftStarted}
                align="left"
              />
            ) : (
              <RollTile
                player="A"
                value={playerRolls.A}
                onRoll={() => {}}
                disabled={true}
                align="left"
              />
            )}
          </div>
          {/* Таймеры игрока A справа от плитки */}
          <div style={{ marginLeft: 12 }}>
            <PlayerTimerPanel
              teamName={teamAName}
              main={mainTimers.A}
              reserve={reserveTimers.A}
              penalty={penaltyTimers.A}
              isActive={currentPlayer === 'A'}
              t={t}
            />
          </div>
        </div>
        {/* Center: Reset button (for judge) at top, then только состояние хода */}
        <div style={{
          flex: 2,
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'flex-start',
          minWidth: 180
        }}>
          {role === 'judge' && (
            <button onClick={handleResetRolls} style={{
              marginBottom: 8,
              background: '#2a3c5a',
              color: '#e3eafc',
              border: 'none',
              borderRadius: 6,
              padding: '6px 14px',
              cursor: 'pointer'
            }}>
              {t.resetRolls}
            </button>
          )}
          {/* Только состояние хода, без надписи и без чекбокса */}
          <div style={{ marginTop: 2 }}>
            <h2 style={{
              color: '#e3eafc',
              fontWeight: 700,
              fontSize: 20,
              marginBottom: 6,
              marginTop: 0,
              letterSpacing: 0.5,
              textAlign: 'center'
            }}>
              {draftStarted
                ? `${t.turn}${currentTurn ? `${currentTurn.phase === 'ban' ? t.ban1 : t.pick} ${currentTurn.team === 'A' ? teamAName : teamBName}` : t.ended}`
                : t.waitingDraft}
            </h2>
          </div>
        </div>
        {/* Roll tile for Player B (right) + таймеры слева от плитки */}
        <div style={{
          flex: 1,
          display: 'flex',
          flexDirection: 'row-reverse',
          alignItems: 'center',
          justifyContent: 'flex-end',
          gap: 10,
          minWidth: 120,
        }}>
          <div>
            {myPlayer === 'B' && !playerRolls.B && !draftStarted ? (
              <RollTile
                player="B"
                value={playerRolls.B}
                onRoll={handlePlayerRoll}
                disabled={!!playerRolls.B || draftStarted}
                align="right"
              />
            ) : (
              <RollTile
                player="B"
                value={playerRolls.B}
                onRoll={() => {}}
                disabled={true}
                align="right"
              />
            )}
          </div>
          {/* Таймеры игрока B слева от плитки */}
          <div style={{ marginRight: 12 }}>
            <PlayerTimerPanel
              teamName={teamBName}
              main={mainTimers.B}
              reserve={reserveTimers.B}
              penalty={penaltyTimers.B}
              isActive={currentPlayer === 'B'}
              t={t}
            />
          </div>
        </div>
      </div>
    );
  }

  // Текущий ход и управление судьей
  function TurnAndJudgeControl() {
    return null;
  }

  // Панель таймеров (больше не используется)
  function Timers() {
    return null;
  }

  // Список агентов
  function AgentListBlock() {
    return (
      <div style={{
        background: 'rgba(40,44,54,0.96)',
        borderRadius: 10,
        // ширина теперь совпадает с центральным блоком (maxWidth: 600, minWidth: 340)
        maxWidth: 1130,
        minWidth: 1130,
        width: '100%',
        marginLeft: '-44%',
        // высота уменьшена, добавлен maxHeight и overflow
        maxHeight: 545,
        overflowY: 'auto',
        padding: '18px 18px 10px 18px',
        boxShadow: '0 2px 8px rgba(20,40,80,0.10)',
        marginBottom: 18,
        position: 'relative'
      }}>
        {/* Кнопка управления драфтом игроков (только для судьи) */}
        {role === 'judge' && (
          <div style={{
            position: 'absolute',
            top: 12,
            right: 18,
            zIndex: 2
          }}>
            <label style={{ color: '#b8c7e0', fontSize: 15, userSelect: 'none', cursor: 'pointer' }}>
              <input
                type="checkbox"
                checked={judgeOverride}
                onChange={() => setJudgeOverride(v => !v)}
                style={{ marginRight: 6 }}
              />
              {t.judgeControl}
            </label>
          </div>
        )}
        <AgentList
          agents={agents}
          isBanned={isBanned}
          isPicked={isPicked}
          isMyTurn={isMyTurn}
          currentTurn={currentTurn}
          handleBanAgent={handleBanAgent}
          setSelectedAgent={(agentName) => {
            // Только для пика открываем модалку
            if (currentTurn?.phase === 'pick' && isMyTurn()) {
              handleAgentPickOpen(agentName);
            }
          }}
          lang={lang}
        />
      </div>
    );
  }

  // Кнопка завершения драфта (только для судьи)
  function EndDraftButton() {
    if (!(role === 'judge' && draftComplete && !finalResult)) return null;
    return (
      <div style={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        width: '100%',
        marginTop: 24,
        marginBottom: 16
      }}>
        <button
          style={{
            background: 'linear-gradient(135deg, #2a3c5a 0%, #3a5a8c 100%)',
            color: '#fff',
            border: 'none',
            borderRadius: 8,
            padding: '10px 20px',
            fontSize: 16,
            fontWeight: 700,
            minWidth: 220,
            boxShadow: '0 2px 8px rgba(20,40,80,0.10)'
          }}
          onClick={openResultModal}
        >
          {t.endDraftAndResult}
        </button>
      </div>
    );
  }

  // Итоговый результат
  function FinalResultBlock() {
    if (!finalResult) return null;
    // Для закрытия комнаты по крестику
    const handleCloseFinalResult = () => {
      if (role === 'judge' && socket && roomId) {
        socket.emit('closeRoom', { roomId });
      }
    };
    return (
      <div style={{
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        zIndex: 4000,
        background: 'rgba(0,0,0,0.45)',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center'
      }}>
        <div style={{
          background: '#23262e',
          padding: 32,
          borderRadius: 16,
          minWidth: 340,
          maxWidth: 420,
          color: '#e3eafc',
          boxShadow: '0 8px 32px rgba(20,40,80,0.22)',
          textAlign: 'center',
          position: 'relative'
        }}>
          {/* Крестик только для судьи */}
          {role === 'judge' && (
            <button
              onClick={handleCloseFinalResult}
              style={{
                position: 'absolute',
                top: 12,
                right: 16,
                background: 'none',
                border: 'none',
                color: '#b8c7e0',
                fontSize: 28,
                cursor: 'pointer',
                zIndex: 10
              }}
              aria-label="Close"
            >×</button>
          )}
          <h2 style={{ color: '#e3eafc', fontWeight: 700, marginBottom: 18 }}>{t.matchResults}</h2>
          <div style={{ color: '#e3eafc', fontSize: 20, fontWeight: 700, marginBottom: 10 }}>
            {finalResult.teamAName}: <b>{Math.round(finalResult.scoreA)}</b>
            <br />
            {finalResult.teamBName}: <b>{Math.round(finalResult.scoreB)}</b>
          </div>
          <h3 style={{ color: '#6ee7b7', fontWeight: 700, fontSize: 22, margin: 0 }}>
            {finalResult.winner === 'draw'
              ? t.draw
              : `${t.winner}${finalResult.winner === 'A' ? finalResult.teamAName : finalResult.teamBName}`}
          </h3>
        </div>
      </div>
    );
  }

  // --- Новое состояние для PickAgentModal ---
  const [pickModalOpen, setPickModalOpen] = useState(false);

  return (
    <div
      style={{
        minHeight: '100vh',
        minWidth: '100vw',
        width: '100vw',
        background: '#181c22',
        boxSizing: 'border-box',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'flex-start',
        padding: 0,
        margin: 0,
        color: '#e3eafc',
        width: '100vw',
        overflowX: 'hidden',
      }}
    >
      <div
        style={{
          display: 'flex',
          flexDirection: 'row',
          width: '100vw',
          maxWidth: '100vw',
          marginLeft: 0,
          marginRight: 0,
          gap: 36,
          position: 'relative',
        }}
      >
        {/* Блок команды A */}
        <div style={{
          position: 'absolute',
          top: 0,
          left: 0,
          width: '33.33%',
          minWidth: 320,
          maxWidth: 420,
          zIndex: 2,
          height: 260,
          display: 'flex',
          alignItems: 'flex-start',
          justifyContent: 'center'
        }}>
          <TeamPanel
            teamName={teamAName}
            picks={picksA}
            bans={bansA}
            getAgentCost={getAgentCost}
            getWEngineCost={getWEngineCost}
            picksCount={picksCount}
            maxTeamValue={maxTeamValue}
            lang={lang}
          />
        </div>
        {/* Центральная часть — теперь разбита на отдельные компоненты */}
        <div style={{
          flex: 1,
          minWidth: 340,
          maxWidth: 600,
          marginLeft: '33.33%',
          marginRight: '33.33%',
          display: 'flex',
          flexDirection: 'column',
          zIndex: 1
        }}>
          <TeamNamesAndCoin />
          <TurnAndJudgeControl />
          <AgentListBlock />
          <EndDraftButton />
          {/* ResultModal теперь всплывающее окно */}
        </div>
        {/* Блок команды B */}
        <div style={{
          position: 'absolute',
          top: 0,
          right: 0,
          width: '33.33%',
          minWidth: 320,
          maxWidth: 420,
          zIndex: 2,
          height: 260,
          display: 'flex',
          alignItems: 'flex-start',
          justifyContent: 'center'
        }}>
          <TeamPanel
            teamName={teamBName}
            picks={picksB}
            bans={bansB}
            getAgentCost={getAgentCost}
            getWEngineCost={getWEngineCost}
            picksCount={picksCount}
            maxTeamValue={maxTeamValue}
            lang={lang}
          />
        </div>
      </div>
      {/* Модальное окно выбора mindscape/w-engine */}
      <PickAgentModal
        show={pickModalOpen}
        agentName={selectedAgent}
        onClose={() => { setPickModalOpen(false); setSelectedAgent(null); }}
        onConfirm={handlePickAgentModalConfirm}
        lang={lang}
      />
      {/* Всплывающее окно результатов */}
      <ResultModal
        show={showResultModal}
        role={role}
        results={results}
        setResults={setResults}
        handlePlayerSubmit={handlePlayerSubmit}
        handleJudgeSubmit={handleJudgeSubmit}
        penaltyTimers={penaltyTimers}
        teamAName={teamAName}
        teamBName={teamBName}
        setShowResultModal={setShowResultModal}
        setTeamAName={setTeamAName}
        setTeamBName={setTeamBName}
        resultsInputs={resultsInputs}
        picksA={picksA}
        picksB={picksB}
        getTeamTotalCost={getTeamTotalCost}
        calcScore={calcScore}
        draftMode={draftMode}
        lang={lang}
      />
      {FinalResultBlock()}
    </div>
  );
}

export default DraftBoard;