import React from 'react';
import { locales } from '../locales';

function AgentList({ agents, isBanned, isPicked, isMyTurn, currentTurn, handleBanAgent, setSelectedAgent, lang = 'ru' }) {
  const t = locales[lang];
  return (
    <>
      <h3 style={{ color: '#e3eafc' }}>{t.agentList}</h3>
      <div
        style={{
          display: 'grid',
          gridTemplateColumns: 'repeat(auto-fill, minmax(78px, 1fr))',
          gap: 10,
          marginTop: 12,
        }}
      >
        {agents.map((agent) => {
          const name = agent.name;
          const displayName = agent.nameLoc?.[lang] || name;
          const banned = isBanned(name);
          const picked = isPicked(name);
          const disabled = banned || picked || !isMyTurn();

          // Новая логика для рамки:
          // - забаненный: красная рамка
          // - выбранный: зеленая рамка
          // - обычный: синяя рамка если активен, серая если disabled
          let borderColor = '#1976d2';
          if (banned) borderColor = '#e53935'; // красная
          else if (picked) borderColor = '#43d675'; // зеленая
          else if (disabled) borderColor = '#444';

          return (
            <div
              key={name}
              style={{
                background: '#23262e',
                borderRadius: 8,
                boxShadow: '0 2px 8px rgba(20,40,80,0.10)',
                opacity: disabled ? 0.4 : 1,
                border: `2px solid ${borderColor}`,
                cursor: disabled ? 'not-allowed' : 'pointer',
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                transition: 'border 0.2s, box-shadow 0.2s',
                minHeight: 98, // 74 (tile) + 24 (label)
                width: 78,
                userSelect: 'none',
                padding: 0,
                position: 'relative',
                overflow: 'hidden', // чтобы рамка была ровной
              }}
              onClick={() => {
                if (disabled) return;
                if (currentTurn?.phase === 'pick') setSelectedAgent(name);
                else if (currentTurn?.phase === 'ban') handleBanAgent(name);
              }}
            >
              {/* Картинка агента на всю плитку */}
              <div
                style={{
                  width: '100%',
                  height: 74,
                  background: '#181c22',
                  borderTopLeftRadius: 8,
                  borderTopRightRadius: 8,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  overflow: 'hidden',
                  position: 'relative'
                }}
              >
                {name && (
                  <img
                    src={require(`../data/Agents_pictures/${name}.png`)}
                    alt={displayName}
                    style={{
                      width: '100%',
                      height: '100%',
                      objectFit: 'cover',
                      borderTopLeftRadius: 8,
                      borderTopRightRadius: 8,
                      background: '#23262e'
                    }}
                  />
                )}
              </div>
              {/* Имя агента под плиткой, ширина как у плитки */}
              <div
                style={{
                  width: '100%',
                  background: 'rgba(24,28,34,0.82)',
                  color: '#e3eafc',
                  fontWeight: 700,
                  fontSize: 13,
                  textAlign: 'center',
                  padding: '3px 2px 4px 2px',
                  borderBottomLeftRadius: 8,
                  borderBottomRightRadius: 8,
                  letterSpacing: 0.1,
                  lineHeight: 1.1,
                  userSelect: 'none',
                  minHeight: 24,
                  maxHeight: 28,
                  overflow: 'hidden',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center'
                }}
              >
                <span style={{
                  width: '100%',
                  overflowWrap: 'break-word',
                  wordBreak: 'break-word',
                  textAlign: 'center',
                  display: 'block'
                }}>
                  {displayName}
                </span>
              </div>
            </div>
          );
        })}
      </div>
    </>
  );
}

export default AgentList;
