import React from 'react';
import { locales } from '../locales';

function TeamPanel({
  teamName,
  picks,
  getAgentCost,
  getWEngineCost,
  picksCount,
  maxTeamValue,
  lang = 'ru',
  bans = [],
}) {
  const t = locales[lang];
  const { agents, wEngines } = require('../data/zzzData');

  // Считаем стоимость команды
  const totalCost = picks.slice(0, picksCount).reduce(
    (sum, { agent, mindscape, wEngine, wEngineRank }) =>
      sum +
      getAgentCost(agent, mindscape) +
      (wEngine ? getWEngineCost(wEngine, typeof wEngineRank === 'number' ? wEngineRank : 0, agent) : 0),
    0
  );

  // Получить объект агента по имени
  const getAgentObj = (name) => agents.find((a) => a.name === name);

  // Получить объект w-engine по имени
  const getWEngineObj = (name) => wEngines.find((w) => w.name === name);

  // Используем локализацию для подписей банов и пиков
  const tileLabels = [
    t.ban1 || (t.ban + '1'), t.ban2 || (t.ban + '2'),
    t.pick + '1', t.pick + '2', t.pick + '3', t.pick + '4', t.pick + '5', t.pick + '6',
    t.pick + '7', t.pick + '8', t.pick + '9'
  ];

  function AgentTile({ agent, mindscape, wEngine, wEngineRank, type, label, banIndex }) {
    const agentObj = getAgentObj(agent);
    const wObj = wEngine ? getWEngineObj(wEngine) : null;
    const displayName = agentObj?.nameLoc?.[lang] || agent || '';
    const hasAgent = !!agent;

    // Красная рамка для Бан 1 и Бан 2
    let borderStyle = hasAgent ? '2.5px solid #1976d2' : '2.5px dashed #3a4a6a';
    if (type === 'ban' && (banIndex === 0 || banIndex === 1)) {
      borderStyle = '2.5px solid #e53935';
    }

    return (
      <div
        style={{
          background: hasAgent ? '#23262e' : '#23262e44',
          borderRadius: 16,
          boxShadow: '0 2px 12px rgba(20,40,80,0.13)',
          minWidth: 120,
          minHeight: 120,
          width: 120,
          height: 120,
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'flex-start',
          margin: 0,
          padding: 0,
          border: borderStyle,
          position: 'relative',
          transition: 'border 0.2s',
          overflow: 'hidden',
        }}
      >
        {/* Картинка агента на всю плитку */}
        {hasAgent && (
          <img
            src={require(`../data/Agents_pictures/${agent}.png`)}
            alt={displayName}
            style={{
              width: '100%',
              height: '100%',
              objectFit: 'cover',
              position: 'absolute',
              top: 0,
              left: 0,
              zIndex: 1,
              borderRadius: 16,
              pointerEvents: 'none'
            }}
          />
        )}
        {/* Плашка на весь размер плитки, появляется только после выбора/бана */}
        {hasAgent && (
          <div
            style={{
              position: 'absolute',
              top: 0,
              left: 0,
              width: '100%',
              height: '100%',
              background: 'rgba(24,28,34,0.60)',
              borderRadius: 16,
              zIndex: 2,
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              justifyContent: 'flex-start',
              pointerEvents: 'none'
            }}
          >
            {/* Имя агента сверху по центру */}
            <div style={{
              width: '100%',
              textAlign: 'center',
              color: '#e3eafc',
              fontWeight: 700,
              fontSize: 16,
              marginTop: 10,
              marginBottom: 0,
              letterSpacing: 0.2,
              textShadow: '0 2px 8px rgba(20,40,80,0.18)'
            }}>
              {displayName}
            </div>
            {/* Mindscape чуть ниже имени */}
            {type !== 'ban' && (
              <div style={{
                marginTop: 8,
                color: '#6ee7b7',
                fontWeight: 700,
                fontSize: 15,
                textAlign: 'center'
              }}>
                M{typeof mindscape === 'number' ? mindscape : ''}
              </div>
            )}
            {/* W-engine картинка и уровень наложения в левом нижнем углу */}
            {type !== 'ban' && wEngine && (
              <div style={{
                position: 'absolute',
                left: 6,
                bottom: 6,
                display: 'flex',
                alignItems: 'center',
                zIndex: 3
              }}>
                <img
                  src={require(`../data/W-engine_pictures/${wEngine}.png`)}
                  alt={wEngine}
                  style={{
                    width: 28,
                    height: 28,
                    objectFit: 'cover',
                    borderRadius: 6,
                    background: '#23262e',
                    border: '1.5px solid #1976d2',
                    marginRight: 4
                  }}
                />
                <span style={{
                  color: '#6ee7b7',
                  fontWeight: 700,
                  fontSize: 14,
                  background: 'rgba(24,28,34,0.75)',
                  borderRadius: 4,
                  padding: '1px 4px'
                }}>
                  R{(typeof wEngineRank === 'number' ? wEngineRank + 1 : 1)}
                </span>
              </div>
            )}
          </div>
        )}
        {/* Надпись "Пик"/"Бан" по центру плитки, только если агент не выбран/не забанен */}
        {!hasAgent && (
          <div
            style={{
              position: 'absolute',
              top: '50%',
              left: '50%',
              transform: 'translate(-50%, -50%)',
              color: '#b8c7e0',
              fontWeight: 700,
              fontSize: 16,
              textAlign: 'center',
              zIndex: 2,
              pointerEvents: 'none',
              // без фона!
            }}
          >
            {label}
          </div>
        )}
      </div>
    );
  }

  // Формируем массив для отображения: 2 бана, 6 пиков (shiyu) или 9 пиков (danger)
  const isDanger = picksCount === 9;
  const tiles = [];
  // Баны
  for (let i = 0; i < 2; ++i) {
    tiles.push(
      <AgentTile
        key={`ban-${i}`}
        agent={bans[i] || ''}
        type="ban"
        label={tileLabels[i]}
        banIndex={i}
      />
    );
  }
  // Пики
  const picksToShow = isDanger ? 9 : 6;
  for (let i = 0; i < picksToShow; ++i) {
    const pick = picks[i] || {};
    tiles.push(
      <AgentTile
        key={`pick-${i}`}
        agent={pick.agent || ''}
        mindscape={pick.mindscape}
        wEngine={pick.wEngine}
        wEngineRank={pick.wEngineRank}
        type="pick"
        label={tileLabels[i + 2]}
      />
    );
  }

  return (
    <div style={{
      background: 'rgba(40,44,54,0.96)',
      borderRadius: 10,
      padding: '32px 18px 32px 18px',
      boxShadow: '0 2px 8px rgba(20,40,80,0.10)',
      color: '#e3eafc',
      minHeight: 220,
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'flex-start'
    }}>
      <h2 style={{
        fontWeight: 800,
        fontSize: 28,
        margin: 0,
        marginBottom: 18,
        color: '#e3eafc',
        textAlign: 'center'
      }}>
        {teamName}
      </h2>
      <div style={{
        fontWeight: 700,
        fontSize: 20,
        color: '#6ee7b7',
        marginBottom: 12,
        textAlign: 'center'
      }}>
        {t.cost} <span style={{ color: '#fff' }}>{totalCost}</span> <span style={{ color: '#b8c7e0' }}>/ {maxTeamValue}</span>
      </div>
      {/* Блоки банов и пиков: 2 в строку, 4 строки (shiyu) или 6 строк (danger) */}
      {isDanger ? (
        <div
          style={{
            width: '100%',
            marginTop: 8,
            maxHeight: 440,
            overflowY: 'auto',
            position: 'relative'
          }}
          className="team-panel-scroll"
        >
          <div style={{
            display: 'grid',
            gridTemplateColumns: '1fr 1fr',
            gap: 14,
          }}>
            {tiles}
          </div>
          <style>
            {`
            .team-panel-scroll::-webkit-scrollbar {
              width: 10px;
              background: #23262e;
              border-radius: 8px;
            }
            .team-panel-scroll::-webkit-scrollbar-thumb {
              background: linear-gradient(135deg, #2a3c5a 0%, #1976d2 100%);
              border-radius: 8px;
              min-height: 32px;
            }
            .team-panel-scroll::-webkit-scrollbar-thumb:hover {
              background: linear-gradient(135deg, #3a5a8c 0%, #1976d2 100%);
            }
            `}
          </style>
        </div>
      ) : (
        <div style={{
          width: '100%',
          marginTop: 8,
        }}>
          <div style={{
            display: 'grid',
            gridTemplateColumns: '1fr 1fr',
            gap: 14,
          }}>
            {tiles}
          </div>
        </div>
      )}
    </div>
  );
}

export default TeamPanel;