import React from "react";
import { locales } from '../locales';

export default function CreateRoomScreen({
  createPassword,
  setCreatePassword,
  createRoom,
  passwordError,
  loading,
  lang = 'ru'
}) {
  const t = locales[lang];
  return (
    <div
      style={{
        minHeight: "100vh",
        minWidth: "100vw",
        width: "100vw",
        height: "100vh",
        background: "#181c22",
        backgroundColor: "#181c22",
        boxSizing: "border-box",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "flex-start",
        fontFamily: "Segoe UI, Arial, sans-serif",
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        overflow: "auto",
      }}
    >
      <div style={{ height: 60 }} />
      <h1
        style={{
          fontSize: 36,
          fontWeight: 800,
          color: "#e3eafc",
          marginBottom: 36,
          textAlign: "center",
          letterSpacing: 1,
          textShadow: "0 2px 8px rgba(40,60,120,0.18)",
        }}
      >
        {t.zzz}
        <br />
        <span style={{ fontWeight: 400, fontSize: 28, color: "#a9b8d2" }}>
          {t.draftTool}
        </span>
      </h1>
      <div
        style={{
          background: "rgba(32,36,44,0.98)",
          boxShadow: "0 8px 32px 0 rgba(20, 30, 60, 0.28)",
          borderRadius: 18,
          padding: "36px 28px 28px 28px",
          minWidth: 320,
          maxWidth: 380,
          width: "90vw",
          marginBottom: 32,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <div style={{ width: "100%", marginBottom: 18 }}>
          <label
            htmlFor="room-password"
            style={{
              fontSize: 17,
              color: "#b8c7e0",
              marginBottom: 10,
              display: "block",
              fontWeight: 500,
              letterSpacing: 0.2,
            }}
          >
            {t.enterPassword}
          </label>
          <form
            onSubmit={(e) => {
              e.preventDefault();
              createRoom();
            }}
            style={{
              display: "flex",
              alignItems: "center",
              gap: 8,
              marginTop: 8,
            }}
          >
            <input
              id="room-password"
              type="password"
              value={createPassword}
              onChange={(e) => setCreatePassword(e.target.value)}
              style={{
                flex: 1,
                padding: "12px 16px",
                fontSize: 18,
                borderRadius: 8,
                border: "1px solid #3a4a6a",
                background: "#23262e",
                color: "#e3eafc",
                outline: "none",
                transition: "border 0.2s",
              }}
              autoFocus
              autoComplete="off"
              placeholder={t.password}
              disabled={loading}
            />
            <button
              type="submit"
              style={{
                background: "linear-gradient(135deg, #2a3c5a 0%, #3a5a8c 100%)",
                border: "none",
                borderRadius: 8,
                width: "56px",
                height: "48px",
                minWidth: "56px",
                minHeight: "48px",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                cursor: loading ? "not-allowed" : "pointer",
                boxShadow: "0 2px 8px rgba(20,40,80,0.18)",
                transition: "background 0.2s",
                marginLeft: 4,
              }}
              disabled={loading}
              aria-label={t.createRoom}
            >
              <svg width="32" height="32" viewBox="0 0 26 26" fill="none">
                <rect
                  x="0"
                  y="0"
                  width="26"
                  height="26"
                  rx="6"
                  fill="rgba(255,255,255,0.08)"
                />
                <path
                  d="M8 13h8M13 8l5 5-5 5"
                  stroke="#b8c7e0"
                  strokeWidth="2.2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
              </svg>
            </button>
          </form>
          {passwordError && (
            <div
              style={{
                color: "#e55",
                marginTop: 10,
                fontSize: 15,
                textAlign: "center",
              }}
            >
              {t.wrongPassword}
            </div>
          )}
        </div>
      </div>
      {/* Информационный блок */}
      <div
        style={{
          position: "fixed",
          right: 18,
          bottom: 18,
          background: "rgba(32,36,44,0.98)",
          borderRadius: 12,
          boxShadow: "0 2px 8px rgba(20,40,80,0.18)",
          padding: "16px 18px",
          fontSize: 13,
          color: "#b8c7e0",
          maxWidth: 320,
          zIndex: 10,
          lineHeight: 1.6,
        }}
      >
        <div style={{ marginBottom: 6 }}>
          {t.notAffiliated}
          <br />
          {t.copyright}
        </div>
        <div style={{ fontWeight: 500, marginBottom: 2 }}>
          {t.madeBy}
        </div>
        <div style={{ color: "#6b7a8b" }}>{t.upToDate}</div>
      </div>
    </div>
  );
}
