import React, { useEffect, useState } from 'react';
import { io } from 'socket.io-client';
import DraftBoard from './components/DraftBoard';
import JoinRoom from './components/JoinRoom'; // если используется
import CreateRoomScreen from './components/CreateRoomScreen';
import { locales } from './locales';

const MAX_TEAM_VALUE = 1100;

const DRAFT_MODES = [
  { value: 'shiyu', label: 'Оборона Шиюй', picks: 6 },
  { value: 'danger', label: 'Опасный штурм', picks: 9 },
];

function App() {
  const [socket, setSocket] = useState(null);
  const [connected, setConnected] = useState(false);
  const [role, setRole] = useState('');
  const [roomId, setRoomId] = useState('');
  const [links, setLinks] = useState(null);
  const [playerTokens, setPlayerTokens] = useState(null);
  const [roomClosed, setRoomClosed] = useState(false);
  const [draftMode, setDraftMode] = useState(DRAFT_MODES[0].value);

  // Новые состояния для названий команд и результата жребия
  const [teamAName, setTeamAName] = useState('Команда A');
  const [teamBName, setTeamBName] = useState('Команда B');
  const [coinResult, setCoinResult] = useState(null);

  // Пароль для создания комнаты
  const [createPassword, setCreatePassword] = useState('');
  const [passwordError, setPasswordError] = useState('');

  // Новое состояние для ожидания игроков
  const [waitingForPlayers, setWaitingForPlayers] = useState(true);

  // Новое состояние для ввода названия команды
  const [teamNameInput, setTeamNameInput] = useState('');
  const [teamNameSet, setTeamNameSet] = useState(() => {
    // Показывать окно только при первом подключении (нет локального значения)
    if (typeof window !== 'undefined') {
      const params = new URLSearchParams(window.location.search);
      const urlRoomId = params.get('roomId');
      const urlRole = params.get('role');
      if (urlRoomId && urlRole && (urlRole === 'playerA' || urlRole === 'playerB')) {
        return !!localStorage.getItem(`zzz-teamname-${urlRoomId}-${urlRole}`);
      }
    }
    return false;
  });


  // Добавьте это состояние:
  const [loading, setLoading] = useState(false);

  // --- Язык интерфейса ---
  const [lang, setLang] = useState(() => {
    if (typeof window !== 'undefined') {
      return localStorage.getItem('zzz-lang') || 'ru';
    }
    return 'ru';
  });

  // Сохраняем язык при изменении
  useEffect(() => {
    if (typeof window !== 'undefined') {
      localStorage.setItem('zzz-lang', lang);
    }
  }, [lang]);

  // --- Фикс: форсируем перерисовку при смене языка ---
  // Используем lang как ключ для корневого div, чтобы все дочерние компоненты пересоздавались при смене языка
  // УДАЛИТЬ этот useEffect, чтобы не пересоздавать сокет и не сбрасывать состояние при смене языка
  // useEffect(() => {
  //   const newSocket = io('http://localhost:3001');
  //   setSocket(newSocket);

  //   const params = new URLSearchParams(window.location.search);
  //   const urlRoomId = params.get('roomId');
  //   const urlRole = params.get('role');
  //   const urlToken = params.get('token');

  //   // Подключаемся только если есть параметры в URL
  //   if (urlRoomId && urlRole) {
  //     newSocket.emit('joinTournament', { roomId: urlRoomId, role: urlRole, token: urlToken });
  //     setRoomId(urlRoomId);
  //     setRole(urlRole);
  //     setConnected(true);
  //     localStorage.setItem('zzz-roomId', urlRoomId);
  //     localStorage.setItem('zzz-role', urlRole);
  //     if (urlToken) localStorage.setItem('zzz-token', urlToken);
  //   }

  //   newSocket.on('joined', ({ roomId, role }) => {
  //     console.log(`✅ Подключён как ${role} к комнате ${roomId}`);
  //   });

  //   // Слушаем событие закрытия комнаты
  //   newSocket.on('roomClosed', () => {
  //     setRoomClosed(true);
  //     setConnected(false);
  //     setRoomId('');
  //     setRole('');
  //     setLinks(null);
  //     setPlayerTokens(null);
  //     localStorage.removeItem('zzz-roomId');
  //     localStorage.removeItem('zzz-role');
  //     localStorage.removeItem('zzz-token');
  //   });

  //   // Слушаем события для названий команд и жребия
  //   newSocket.on('teamNames', ({ teamA, teamB }) => {
  //     setTeamAName(teamA || 'Команда А');
  //     setTeamBName(teamB || 'Команда Б');
  //   });
  //   newSocket.on('coinFlip', (result) => {
  //     setCoinResult(result);
  //   });

  //   newSocket.on('roomState', (state) => {
  //     if (state.teamAName) setTeamAName(state.teamAName);
  //     if (state.teamBName) setTeamBName(state.teamBName);
  //     if (typeof state.coinResult !== 'undefined') setCoinResult(state.coinResult);
  //     if (typeof state.waitingForPlayers !== 'undefined') setWaitingForPlayers(state.waitingForPlayers);
  //   });

  //   return () => {
  //     newSocket.disconnect();
  //   };
  // }, []);

  // Вместо этого, инициализируем сокет только один раз при монтировании
  useEffect(() => {
    const newSocket = io('http://localhost:3001');
    setSocket(newSocket);

    const params = new URLSearchParams(window.location.search);
    const urlRoomId = params.get('roomId');
    const urlRole = params.get('role');
    const urlToken = params.get('token');

    // Подключаемся только если есть параметры в URL
    if (urlRoomId && urlRole) {
      newSocket.emit('joinTournament', { roomId: urlRoomId, role: urlRole, token: urlToken });
      setRoomId(urlRoomId);
      setRole(urlRole);
      setConnected(true);
      localStorage.setItem('zzz-roomId', urlRoomId);
      localStorage.setItem('zzz-role', urlRole);
      if (urlToken) localStorage.setItem('zzz-token', urlToken);
    }

    newSocket.on('joined', ({ roomId, role }) => {
      console.log(`✅ Подключён как ${role} к комнате ${roomId}`);
    });

    // Слушаем событие закрытия комнаты
    newSocket.on('roomClosed', () => {
      setRoomClosed(true);
      setConnected(false);
      setRoomId('');
      setRole('');
      setLinks(null);
      setPlayerTokens(null);
      localStorage.removeItem('zzz-roomId');
      localStorage.removeItem('zzz-role');
      localStorage.removeItem('zzz-token');
    });

    // Слушаем события для названий команд и жребия
    newSocket.on('teamNames', ({ teamA, teamB }) => {
      setTeamAName(teamA || 'Команда А');
      setTeamBName(teamB || 'Команда Б');
    });
    newSocket.on('coinFlip', (result) => {
      setCoinResult(result);
    });

    newSocket.on('roomState', (state) => {
      if (state.teamAName) setTeamAName(state.teamAName);
      if (state.teamBName) setTeamBName(state.teamBName);
      if (typeof state.coinResult !== 'undefined') setCoinResult(state.coinResult);
      if (typeof state.waitingForPlayers !== 'undefined') setWaitingForPlayers(state.waitingForPlayers);
    });

    return () => {
      newSocket.disconnect();
    };
  }, []); // <-- пустой массив зависимостей, чтобы сокет создавался только один раз

  // Автоматическое закрытие комнаты при уходе судьи (перезагрузка/закрытие вкладки)
  useEffect(() => {
    if (role === 'judge' && roomId && socket) {
      const handleBeforeUnload = () => {
        socket.emit('closeRoom', { roomId });
      };
      window.addEventListener('beforeunload', handleBeforeUnload);
      return () => {
        window.removeEventListener('beforeunload', handleBeforeUnload);
      };
    }
  }, [role, roomId, socket]);

  // --- Новый пароль для создания комнаты ---
  const REQUIRED_PASSWORD = 'Dc0FC]D]~vxFIsE3XY@E>4&rE;mW\\kxKg{6U.3F@Mz:1S/:ckAsY2U{^9Lc}T@?N'; // обновлённый пароль

  const createRoom = () => {
    setPasswordError('');
    setLoading(true);
    if (createPassword !== REQUIRED_PASSWORD) {
      setPasswordError('Неверный пароль для создания комнаты!');
      setLoading(false);
      return;
    }
    const newRoomId = Math.random().toString(36).slice(2, 8);
    // Генерируем уникальные токены для игроков
    const tokenA = Math.random().toString(36).slice(2, 10);
    const tokenB = Math.random().toString(36).slice(2, 10);
    setPlayerTokens({ playerA: tokenA, playerB: tokenB });

    // Исправлено: передаем пароль на сервер
    socket.emit('joinTournament', { roomId: newRoomId, role: 'judge', password: createPassword });
    setRoomId(newRoomId);
    setRole('judge');
    setConnected(true);
    setLoading(false);
    // Сохраняем в localStorage
    localStorage.setItem('zzz-roomId', newRoomId);
    localStorage.setItem('zzz-role', 'judge');
    localStorage.removeItem('zzz-token');
    setLinks({
      playerA: `${window.location.origin}/?roomId=${newRoomId}&role=playerA&token=${tokenA}`,
      playerB: `${window.location.origin}/?roomId=${newRoomId}&role=playerB&token=${tokenB}`,
    });
  };

  const closeRoom = () => {
    if (socket && roomId) {
      socket.emit('closeRoom', { roomId });
    }
  };

    // Кнопка выбора языка (отображается всегда)
  const LangSwitcher = (
    <div style={{ position: 'absolute', top: 18, right: 18, zIndex: 1000 }}>
      <button
        onClick={() => setLang('ru')}
        style={{
          fontWeight: lang === 'ru' ? 700 : 400,
          background: lang === 'ru' ? '#1976d2' : '#eee',
          color: lang === 'ru' ? '#fff' : '#222',
          border: 'none',
          borderRadius: 6,
          padding: '6px 14px',
          marginRight: 6,
          cursor: 'pointer'
        }}
        type="button"
      >
        RU
      </button>
      <button
        onClick={() => setLang('en')}
        style={{
          fontWeight: lang === 'en' ? 700 : 400,
          background: lang === 'en' ? '#1976d2' : '#eee',
          color: lang === 'en' ? '#fff' : '#222',
          border: 'none',
          borderRadius: 6,
          padding: '6px 14px',
          cursor: 'pointer'
        }}
        type="button"
      >
        EN
      </button>
    </div>
  );
  
  // Синхронизация режима драфта между всеми клиентами
  useEffect(() => {
    if (!socket) return;

    // Получаем режим драфта от сервера
    socket.on('draftModeSync', (mode) => {
      setDraftMode(mode);
    });

    return () => {
      socket.off('draftModeSync');
    };
  }, [socket]);

  // Судья отправляет выбранный режим на сервер
  useEffect(() => {
    if (!socket) return;
    if (role === 'judge') {
      socket.emit('draftModeSync', { roomId, draftMode });
    }
    // eslint-disable-next-line
  }, [draftMode, role, roomId, socket]);

  // --- Notification state for copy link ---
  const [copyNotification, setCopyNotification] = useState('');

  // --- Notification component ---
  function CopyNotification() {
    if (!copyNotification) return null;
    return (
      <div style={{
        position: 'fixed',
        right: 24,
        bottom: 24,
        zIndex: 9999,
        background: '#23262e',
        color: '#6ee7b7',
        fontWeight: 700,
        fontSize: 16,
        borderRadius: 10,
        boxShadow: '0 2px 8px rgba(20,40,80,0.18)',
        padding: '16px 32px',
        opacity: 0.98,
        transition: 'opacity 0.3s'
      }}>
        {copyNotification}
      </div>
    );
  }

  // Показываем поле для ввода названия команды для игроков только при первом подключении
  if (
    connected &&
    (role === 'playerA' || role === 'playerB') &&
    !teamNameSet
  ) {
    return (
      <div style={{
        minHeight: "100vh",
        minWidth: "100vw",
        width: "100vw",
        background: "#181c22",
        boxSizing: "border-box",
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        overflow: "auto"
      }}>
        {LangSwitcher}
        <div style={{ padding: 40, maxWidth: 400, margin: '40px auto', background: '#23262e', borderRadius: 10 }}>
          <h2 style={{ color: "#e3eafc" }}>{locales[lang].enterTeamName}</h2>
          <form
            onSubmit={e => {
              e.preventDefault();
              if (!teamNameInput.trim()) return;
              if (role === 'playerA') {
                setTeamAName(teamNameInput.trim());
                setTeamNameSet(true);
                localStorage.setItem(`zzz-teamname-${roomId}-playerA`, teamNameInput.trim());
                socket.emit('teamNames', { roomId, teamA: teamNameInput.trim(), teamB: teamBName });
              } else if (role === 'playerB') {
                setTeamBName(teamNameInput.trim());
                setTeamNameSet(true);
                localStorage.setItem(`zzz-teamname-${roomId}-playerB`, teamNameInput.trim());
                socket.emit('teamNames', { roomId, teamA: teamAName, teamB: teamNameInput.trim() });
              }
            }}
          >
            <input
              type="text"
              value={teamNameInput}
              onChange={e => setTeamNameInput(e.target.value)}
              placeholder={locales[lang].teamName}
              style={{ width: '100%', padding: 10, fontSize: 18, marginBottom: 16, background: "#23262e", color: "#e3eafc", border: "1px solid #3a4a6a", borderRadius: 6 }}
              autoFocus
              required
            />
            <button
              type="submit"
              style={{
                background: '#1976d2',
                color: '#fff',
                padding: '10px 20px',
                border: 'none',
                borderRadius: 4,
                cursor: 'pointer',
                fontSize: 16
              }}
            >
              {locales[lang].continue}
            </button>
          </form>
        </div>
      </div>
    );
  }

  if (roomClosed) {
    return (
      <div style={{
        minHeight: "100vh",
        minWidth: "100vw",
        width: "100vw",
        background: "#181c22",
        boxSizing: "border-box",
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        overflow: "auto"
      }}>
        {LangSwitcher}
        <div style={{ padding: 40, textAlign: 'center', color: "#e3eafc" }}>
          <h2>{locales[lang].roomClosed}</h2>
          <p>{locales[lang].draftFinished}</p>
        </div>
      </div>
    );
  }

  if (!connected) {
    return (
      <div style={{
        minHeight: "100vh",
        minWidth: "100vw",
        width: "100vw",
        background: "#181c22",
        boxSizing: "border-box",
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        overflow: "auto",
        fontFamily: "'Segoe UI', 'Arial', 'Helvetica Neue', sans-serif"
      }}>
        {LangSwitcher}
        <CreateRoomScreen
          createPassword={createPassword}
          setCreatePassword={setCreatePassword}
          createRoom={() => {
            // Создаём сокет если его нет
            if (!socket) {
              const newSocket = io('http://localhost:3001');
              setSocket(newSocket);
              // После установки сокета, повторно вызвать createRoom
              setTimeout(() => {
                setCreatePassword(p => p); // триггер перерисовки
              }, 0);
              return;
            }
            createRoom();
          }}
          passwordError={passwordError}
          loading={loading}
          lang={lang}
        />
      </div>
    );
  }

  // Передаем режим и язык в DraftBoard
  return (
    <div style={{
      minHeight: "100vh",
      minWidth: "100vw",
      width: "100vw",
      height: "100vh",
      background: "#181c22",
      boxSizing: "border-box",
      overflow: "hidden", // заменено с "auto" на "hidden"
      position: "absolute",
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      fontFamily: "'Segoe UI', 'Arial', 'Helvetica Neue', sans-serif",
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
    }}>
      {/* Языковой переключатель теперь статичный */}
      <div style={{ position: 'absolute', top: 18, right: 18, zIndex: 1000 }}>
        <button
          onClick={() => setLang('ru')}
          style={{
            fontWeight: lang === 'ru' ? 700 : 400,
            background: lang === 'ru' ? '#1976d2' : '#eee',
            color: lang === 'ru' ? '#fff' : '#222',
            border: 'none',
            borderRadius: 6,
            padding: '6px 14px',
            marginRight: 6,
            cursor: 'pointer'
          }}
          type="button"
        >
          RU
        </button>
        <button
          onClick={() => setLang('en')}
          style={{
            fontWeight: lang === 'en' ? 700 : 400,
            background: lang === 'en' ? '#1976d2' : '#eee',
            color: lang === 'en' ? '#fff' : '#222',
            border: 'none',
            borderRadius: 6,
            padding: '6px 14px',
            cursor: 'pointer'
          }}
          type="button"
        >
          EN
        </button>
      </div>
      {/* Room ID, Role, and Close Room button: absolute, not fixed */}
      <div style={{
        position: "absolute",
        top: 18,
        left: 24,
        zIndex: 1001,
        display: "flex",
        flexDirection: "column",
        alignItems: "flex-start"
      }}>
        <div style={{
          fontWeight: 800,
          fontSize: 16.25,
          color: "#e3eafc",
          letterSpacing: 1,
          marginBottom: 4,
          textShadow: "0 2px 8px rgba(40,60,120,0.18)"
        }}>
          {locales[lang].room}<b>{roomId}</b>
        </div>
        <div style={{
          fontWeight: 700,
          fontSize: 15,
          color: "#e3eafc",
          marginBottom: 4
        }}>
          {locales[lang].role}<b>{role}</b>
        </div>
        {role === 'judge' && (
          <button
            onClick={closeRoom}
            style={{
              background: "linear-gradient(135deg, #e55 0%, #a33 100%)",
              color: "#fff",
              fontWeight: 700,
              fontSize: 12.5, // 10 * 1.25
              border: "none",
              borderRadius: 8,
              padding: "7.8px 18.75px", // 6.25*1.25, 15*1.25
              marginTop: 2,
              marginBottom: 8,
              boxShadow: "0 2px 8px rgba(80,20,20,0.18)",
              cursor: "pointer",
              transition: "background 0.2s"
            }}
          >
            {locales[lang].closeRoom}
          </button>
        )}
      </div>
      {/* Centered title and player links/tokens: absolute, not fixed */}
      <div style={{
        width: "100vw",
        position: "absolute",
        top: 18,
        left: 0,
        zIndex: 1000,
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        pointerEvents: "none"
      }}>
        <h1 style={{
          fontSize: 32,
          fontWeight: 800,
          color: "#e3eafc",
          margin: 0,
          letterSpacing: 1,
          textShadow: "0 2px 8px rgba(40,60,120,0.18)",
          pointerEvents: "auto"
        }}>
          {locales[lang].zzzDraft}
        </h1>
        {role === 'judge' && links && (
          <div style={{
            display: "flex",
            gap: 24,
            marginTop: 18,
            pointerEvents: "auto"
          }}>
            <button
              onClick={() => {
                navigator.clipboard.writeText(links.playerA);
                setCopyNotification('Ссылка для Player A скопирована!');
                setTimeout(() => setCopyNotification(''), 2000);
              }}
              style={{
                background: "linear-gradient(135deg, #2a3c5a 0%, #3a5a8c 100%)",
                color: "#e3eafc",
                fontWeight: 700,
                fontSize: 13,
                border: "none",
                borderRadius: 8,
                padding: "7px 16px",
                boxShadow: "0 2px 8px rgba(20,40,80,0.18)",
                cursor: "pointer",
                transition: "background 0.2s"
              }}
            >
              {locales[lang].copyLinkA}
            </button>
            <button
              onClick={() => {
                navigator.clipboard.writeText(links.playerB);
                setCopyNotification('Ссылка для Player B скопирована!');
                setTimeout(() => setCopyNotification(''), 2000);
              }}
              style={{
                background: "linear-gradient(135deg, #2a3c5a 0%, #3a5a8c 100%)",
                color: "#e3eafc",
                fontWeight: 700,
                fontSize: 13,
                border: "none",
                borderRadius: 8,
                padding: "7px 16px",
                boxShadow: "0 2px 8px rgba(20,40,80,0.18)",
                cursor: "pointer",
                transition: "background 0.2s"
              }}
            >
              {locales[lang].copyLinkB}
            </button>
          </div>
        )}
        {role === 'judge' && links && (
          <div style={{
            display: "flex",
            gap: 24,
            marginTop: 8,
            pointerEvents: "auto"
          }}>
            <div style={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
              <span style={{
                fontSize: 13,
                color: "#7d8ca8",
                marginBottom: 2,
                fontWeight: 500
              }}>
                {locales[lang].tokenA}
              </span>
              <span
                style={{
                  background: "#111",
                  color: "transparent",
                  borderRadius: 4,
                  padding: "2px 8px",
                  cursor: "pointer",
                  transition: "color 0.2s",
                  userSelect: "none",
                  position: "relative",
                  fontSize: 15,
                  letterSpacing: 1
                }}
                onMouseEnter={e => {
                  e.currentTarget.style.color = "#fff";
                }}
                onMouseLeave={e => {
                  e.currentTarget.style.color = "transparent";
                }}
                title={locales[lang].pointToShowToken}
              >
                {playerTokens?.playerA}
              </span>
              <span style={{
                marginTop: 8,
                fontWeight: 800,
                fontSize: 22,
                color: "#e3eafc"
              }}>
                {teamAName}
              </span>
            </div>
            <div style={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
              <span style={{
                fontSize: 13,
                color: "#7d8ca8",
                marginBottom: 2,
                fontWeight: 500
              }}>
                {locales[lang].tokenB}
              </span>
              <span
                style={{
                  background: "#111",
                  color: "transparent",
                  borderRadius: 4,
                  padding: "2px 8px",
                  cursor: "pointer",
                  transition: "color 0.2s",
                  userSelect: "none",
                  position: "relative",
                  fontSize: 15,
                  letterSpacing: 1
                }}
                onMouseEnter={e => {
                  e.currentTarget.style.color = "#fff";
                }}
                onMouseLeave={e => {
                  e.currentTarget.style.color = "transparent";
                }}
                title={locales[lang].pointToShowToken}
              >
                {playerTokens?.playerB}
              </span>
              <span style={{
                marginTop: 8,
                fontWeight: 800,
                fontSize: 22,
                color: "#e3eafc"
              }}>
                {teamBName}
              </span>
            </div>
          </div>
        )}
      </div>
      <div style={{
        padding: "100px 20px 20px 20px",
        fontFamily: "'Segoe UI', 'Arial', 'Helvetica Neue', sans-serif",
        color: "#e3eafc",
        maxWidth: 1600,
        margin: "0 auto",
        width: "100%",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        overflow: "visible"
      }}>
        {/* Убрана надпись "Роль:" */}
        {/* Показываем названия команд и результат жребия */}
       
        {/* Кнопка выбора режима по центру строки, чуть ниже */}
        <div style={{
          margin: "100px 0 24px 0", // увеличен верхний отступ
          display: "flex",
          alignItems: "center",
          justifyContent: "center"
        }}>
          <label style={{
            fontWeight: 700,
            color: "#e3eafc",
            fontSize: 16,
            display: "flex",
            alignItems: "center"
          }}>
            <span style={{ marginRight: 10 }}>{locales[lang].draftMode}</span>
            <select
              value={draftMode}
              onChange={e => setDraftMode(e.target.value)}
              disabled={role !== 'judge'}
              style={{
                fontSize: 16,
                marginLeft: 0,
                background: "#23262e",
                color: "#e3eafc",
                border: "1px solid #3a4a6a",
                borderRadius: 6,
                padding: "6px 14px",
                fontWeight: 600,
                cursor: role === 'judge' ? "pointer" : "not-allowed"
              }}
            >
              <option value="shiyu">{locales[lang].shiyu}</option>
              <option value="danger">{locales[lang].danger}</option>
            </select>
          </label>
        </div>
        {/* Ожидание игроков */}
        {waitingForPlayers ? (
          <div style={{
            background: '#222',
            color: '#fff',
            padding: 24,
            borderRadius: 10,
            margin: '24px 0',
            textAlign: 'center',
            fontSize: 20
          }}>
            {locales[lang].waitingPlayers}
          </div>
        ) : (
          // Компонент драфта только если все игроки на месте
          <DraftBoard
            roomId={roomId}
            role={role}
            socket={socket}
            maxTeamValue={MAX_TEAM_VALUE}
            draftMode={draftMode}
            lang={lang}
          />
        )}
      </div>
      {CopyNotification()}
    </div>
  );
}

export default App;